/**
 * Возвращает случайное целое число от 0 до num-1.
 *
 * @param {number} num - Число, определяющее верхнюю границу диапазона.
 * @returns {number} Случайное целое число.
 */
export const randomNumber = (num) => {
    return Math.floor(Math.random() * num);
};

export const nul = () => false;

/**
 * Выполняет функцию для каждого элемента массива, объекта, числа или строки.
 *
 * @param {(Array|Object|number|string)} unknown - Коллекция элементов для итерации.
 * @param {Function} fn - Функция, выполняемая для каждого элемента.
 */
export const each = (unknown, fn) => {
    if (Array.isArray(unknown)) {
        unknown = [ ...unknown ];
        unknown.forEach(fn);
    } else if (typeof (unknown) == 'object') {
        const blank = {};
        for (const i in unknown) {
            if (blank[i]) continue;
            if (fn(unknown[i], i) === false) break;
        }
    } else if (typeof (unknown) == 'number') {
        for (let j = 0; j < unknown; j++) {
            if (fn(j, j) === false) break;
        }
    } else if (typeof (unknown) == 'string') {
        for (let k = 0, l = unknown.length; k < l; k++) {
            if (fn(unknown.charAt(k), k) === false) break;
        }
    }
};

/**
 * Применяет свойства из одного объекта к другому, за исключением указанных.
 *
 * @param {Object} form - Объект, содержащий свойства для применения.
 * @param {Object} [to={}] - Объект, к которому будут применены свойства.
 * @param {Object} [except] - Объект, содержащий свойства, которые не должны быть применены.
 * @returns {Object} Объект с применёнными свойствами.
 */
export const apply = (form, to, except) => {
    if (!to) to = {};
    if (except) each(form, (item, key) => {
        if (key in except) return;
        to[key] = item;
    });
    else each(form, (item, key) => to[key] = item);

    return to;
};


/**
 * Создаёт HTML элемент с заданными атрибутами.
 *
 * @param {string} tagName - Имя тега создаваемого элемента.
 * @param {Object} attributes - Атрибуты, которые будут применены к элементу.
 * @param {string} [attributes.class] - Класс элемента.
 * @param {string} [attributes.for] - Атрибут `for` элемента.
 * @param {(string|Object)} [attributes.style] - Стили элемента.
 * @returns {HTMLElement} Созданный HTML элемент.
 */
export const makeElement = (tagName, attributes) => {
    const el       = document.createElement(tagName);
    const setStyle = (unknown) => {
        if (typeof unknown == 'string') el.style.cssText = unknown;
        else apply(unknown, el.style);
    };

    for (const prop in attributes) {
        if (prop === 'class') el.className = attributes[prop];
        else if (prop === 'for') el.htmlFor = attributes[prop];
        else if (prop === 'style') setStyle(attributes[prop]);
        else el.setAttribute(prop, attributes[prop]);
    }

    return el;
};

/**
 * Обрабатывает неизвестный тип данных и кэширует его.
 *
 * @param {(string|number|Object|Function)} unknown - Неизвестный тип данных для обработки.
 * @returns {(string|number|Object|Function)} Возвращает кэшированное значение или номер кэша.
 */
export const handle = (unknown) => {
    const fn = handle;

    let type;
    let number;

    if (!fn.cache) {
        fn.cache = {};
    }

    if (typeof fn.number === 'undefined') {
        fn.number = 0;
    }

    type = typeof unknown;

    if (type === 'string') {
        return fn.cache[unknown];
    } else if (type === 'number') {
        return fn.cache[unknown.toString()];
    } else if (type === 'object' || type === 'function') {
        number                      = fn.number++;
        fn.cache[number.toString()] = unknown;
        return number;
    }
}
