import { Config } from '../config';
import { Control } from '../scripts/control';
import { LayerManager } from '../managers/layer.manager';
import { Flash } from '../object/flash';
import { randomNumber } from '../scripts/utils';
import { Fruit } from '../object/fruit';


export class FruitFactory {
    static fruitCache: Fruit[] = [];

    _safeNumber: number = 0;

    constructor (
        private readonly _config: Config,
        private readonly _control: Control,
        private readonly _layerManager: LayerManager,
        private readonly _flash: Flash,
    ) {
        this.safeNumber = this._config.options.FIRST_SAFE_FRUIT_NUMBER;
    }

    get safeNumber () {
        return this._safeNumber;
    }

    set safeNumber (number) {
        this._safeNumber = Math.max(number, 0);
    }

    create (originX: number, originY: number) {
        const type  = this.getType();
        const fruit = new Fruit(
            this._config,
            this._control,
            this._layerManager,
            this._flash,
            { type, originX, originY },
        ).set();

        FruitFactory.fruitCache.unshift(fruit);

        return fruit;
    }

    getType () {
        const { FRUIT_CONFIG: { types } } = this._config.options;
        const fruitTypes                  = [ types.FIRST, types.SECOND, types.THIRD, types.FOURTH, types.FIFTH ];
        const isBoom                      = randomNumber(8) === 4 && !this.safeNumber;

        this.safeNumber = this.safeNumber - 1;

        return isBoom ? types.BOOM : fruitTypes[randomNumber(fruitTypes.length)];
    }

    getFruitInView () {
        return FruitFactory.fruitCache;
    }
}
