import buzz from '../libs/buzz';
import { SoundButton } from './sound-button';


export class Sound {
    sound: any;

    constructor (
        private readonly _src: string,
        private readonly _soundButton: SoundButton
    ) { // @ts-ignore
        this.sound = new buzz.sound(this._src, {
            formats : [ 'ogg', 'mp3' ],
            preload : true,
            autoload: true,
            loop    : false,
        });
    }

    play () {
        this.sound.setPercent(0);
        this._soundButton.isOn && this.sound.play();
        this._soundButton.isOn && this.sound.fadeIn(500);
    }

    stop () {
        if (this._soundButton.isOn) {
            this.sound.fadeOut(1000, this.sound.pause.bind(this.sound));
        } else {
            this.sound.stop();
        }
    }
}
