import tween from '../libs/tween';
import { Config } from '../config';
import { Scenes, SoundNames } from '../config/types';

import { DisplacementFactory } from '../factories/displacement.factory';
import { SoundFactory } from '../factories/sound.factory';

import { HomeScene } from '../scenes/home.scene';
import { GameOverScene } from '../scenes/game-over.scene';
import { ResultScene } from '../scenes/result.scene';
import { BrandIntroScene } from '../scenes/brand-intro.scene';
import { BrandQuestionScene } from '../scenes/brand-question.scene';

import message from '../scripts/message';
import timeline from '../scripts/timeline';
import state from '../scripts/state';
import { Game } from '../scripts/game';

import { LayerManager } from './layer.manager';

import { Displacement } from '../object/displacement';
import { Sound } from '../object/sound';
import { Background } from '../object/background';
import { Score } from '../object/score';
import { Lose } from '../object/lose';
import { Flash } from '../object/flash';
import { FPS } from '../object/fps';
import { SoundButton } from '../object/sound-button';



export class ScenesManager {
    _startScene: HomeScene;
    _gameOverScene: GameOverScene;
    _resultScene: ResultScene;
    _brandIntroScene: BrandIntroScene;
    _brandQuestionScene: BrandQuestionScene;
    _bottom: Displacement;
    _fps: FPS;

    menuSnd: Sound;
    gameStartSnd: Sound;
    setTimeout  = timeline.setTimeout.bind(timeline);
    setInterval = timeline._setInterval.bind(timeline);

    constructor (
        private readonly _config: Config,
        private readonly _game: Game,
        private readonly _layerManager: LayerManager,
        private readonly _soundButton: SoundButton,
        private readonly _soundFactory: SoundFactory,
        private readonly _flash: Flash,
        private readonly _score: Score,
        private readonly _lose: Lose,
        private readonly _background: Background,
        private readonly _displacementFactory: DisplacementFactory,
    ) {
        const { LAYERS, BOTTOM_CONFIG } = this._config.options;

        this._bottom     = this._displacementFactory.create({
            layer   : LAYERS.DEFAULT,
            imageSrc: BOTTOM_CONFIG.image,
            width   : BOTTOM_CONFIG.width,
            height  : BOTTOM_CONFIG.height,
            startX  : BOTTOM_CONFIG.startX,
            startY  : BOTTOM_CONFIG.startY,
            endX    : BOTTOM_CONFIG.endX,
            endY    : BOTTOM_CONFIG.endY,
            animMap : tween.exponential.co,
            duration: BOTTOM_CONFIG.duration,
        });

        this._startScene         = new HomeScene(_config, _layerManager);
        this._gameOverScene      = new GameOverScene(_config, _layerManager);
        this._resultScene        = new ResultScene(_config, _layerManager, _game);
        this._brandIntroScene    = new BrandIntroScene(_config, _layerManager);
        this._brandQuestionScene = new BrandQuestionScene(_config, _layerManager);
        this._fps           = new FPS(_config, _layerManager);

        const { SOUNDS, MESSAGES } = this._config.options;

        this.menuSnd      = this._soundFactory.create(SOUNDS[SoundNames.MENU]);
        this.gameStartSnd = this._soundFactory.create(SOUNDS[SoundNames.START]);


        message.addEventListener(MESSAGES.SCENE_SWITCH_SCENE, (name: (typeof Scenes)[keyof typeof Scenes]) => {
            this.switchScene(name);
        });

        message.addEventListener(MESSAGES.TOGGLE_SOUND, () => {
            this.toggleSound();
        });
    }

    init () {
        [
            this._background,
            this._startScene,
            this._score,
            this._lose,
            this._gameOverScene,
            this._flash,
            this._bottom,
            this._resultScene,
            this._soundButton,
            this._brandQuestionScene,
            this._brandIntroScene,
            this._fps,
        ].invoke('set');

        this.setInterval(this._fps.update, 500);
    }

    switchScene (name: (typeof Scenes)[keyof typeof Scenes]) {
        const { SCENE_STATES, STATE_KEYS, SCENES } = this._config.options;
        const onShow                               = () => sceneState.set(SCENE_STATES.READY);
        const curScene                             = state(STATE_KEYS.SCENE_NAME);
        const sceneState                           = state(STATE_KEYS.SCENE_STATE);

        if (curScene.is(name)) return;

        const onHide = () => {
            curScene.set(name);
            sceneState.set(SCENE_STATES.ENTERING);
            switch (name) {
                case SCENES.HOME_MENU:
                    this.showMenu(onShow);
                    break;
                case SCENES.GAME_BODY:
                    this.showGame(onShow);
                    break;
                case SCENES.GAME_OVER:
                    this.showGameOver(onShow);
                    break;
                case SCENES.GAME_RESULT:
                    this.showResult(onShow);
                    break;
                case SCENES.BRAND_INTRO:
                    this.showBrandIntro(onShow);
                    break;
                case SCENES.BRAND_QUESTION:
                    this.showBrandQuestion(onShow);
                    break;
            }
        };

        sceneState.set(SCENE_STATES.EXITING);

        if (curScene.isunset()) onHide();
        else if (curScene.is(SCENES.HOME_MENU)) this.hideMenu(onHide);
        else if (curScene.is(SCENES.GAME_BODY)) this.hideGame(onHide);
        else if (curScene.is(SCENES.GAME_OVER)) this.hideGameOver(onHide);
        else if (curScene.is(SCENES.GAME_RESULT)) this.hideResult(onHide);
        else if (curScene.is(SCENES.BRAND_INTRO)) this.hideBrandIntro(onHide);
        else if (curScene.is(SCENES.BRAND_QUESTION)) this.hideBrandQuestion(onHide);
    }

    toggleSound () {
        const { SCENES, STATE_KEYS } = this._config.options;
        const curScene               = state(STATE_KEYS.SCENE_NAME);
        const method                 = this._soundButton.isOn ? 'play' : 'stop';

        if (curScene.is(SCENES.HOME_MENU)) this.menuSnd[method]();
        else if (curScene.is(SCENES.GAME_BODY)) this.gameStartSnd[method]();
        else if (curScene.is(SCENES.GAME_RESULT)) this.menuSnd[method]();
    }

    showMenu (callback: () => void) {
        const { MODES, LAYERS } = this._config.options;
        this._layerManager.showLayer(LAYERS.START_SCREEN);

        const group = [
            [ this._startScene, 0 ],
            [ this._bottom, 1000 ],
        ];

        group.invoke(MODES.SHOW);

        this.menuSnd.play();

        this._soundButton.showHomeSoundButton(1200);

        this.setTimeout(callback, 2500);
    }

    hideMenu (callback: () => void) {
        const { MODES, LAYERS } = this._config.options;
        [ this._startScene ].invoke(MODES.HIDE);

        this._soundButton.hideHomeSoundButton();

        this.menuSnd.stop();

        this.setTimeout(() => {
            callback();
            this._layerManager.hideLayer(LAYERS.START_SCREEN);
        }, 1200);
    }

    showGame (callback: () => void) {
        const { LAYERS } = this._config.options;
        this._layerManager.showLayer(LAYERS.FRUIT);

        this._score.show();
        this._lose.show();
        this._game.start();

        this.gameStartSnd.play();

        this._soundButton.showGameSoundButton();

        this.setTimeout(callback, 700);
    }

    hideGame (callback: () => void) {
        const { LAYERS } = this._config.options;
        this._score.hide();
        this._lose.hide();

        this._soundButton.hideGameSoundButton();

        this.gameStartSnd.stop();

        this.setTimeout(() => {
            callback();
            this._layerManager.hideLayer(LAYERS.FRUIT);
        }, 700);
    }

    showGameOver (callback: () => void) {
        const { SCENES, MODES } = this._config.options;
        const group             = [
            [ this._gameOverScene, 0 ],
        ];

        group.invoke(MODES.SHOW);

        this.setTimeout(() => {
            callback();
            this.switchScene(SCENES.GAME_RESULT);
        }, 2000);
    }

    hideGameOver (callback: () => void) {
        const { MODES } = this._config.options;
        [ this._gameOverScene ].invoke(MODES.HIDE);

        this.setTimeout(callback, 1000);
    }

    showResult (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        this._layerManager.showLayer(LAYERS.RESULT_SCREEN);

        const group = [
            [ this._resultScene, 0 ],
        ];

        group.invoke(MODES.SHOW);

        this.menuSnd.play();

        this._soundButton.showResultSoundButton();

        this.setTimeout(callback);
    }

    hideResult (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        [ this._resultScene ].invoke(MODES.HIDE);

        this._soundButton.hideResultSoundButton();

        this.menuSnd.stop();

        this.setTimeout(() => {
            callback();
            this._layerManager.hideLayer(LAYERS.RESULT_SCREEN);
        }, 1000);
    }

    showBrandIntro (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        this._layerManager.showLayer(LAYERS.BRAND_INTRO);

        const group = [
            [ this._brandIntroScene, 0 ],
        ];

        group.invoke(MODES.SHOW);

        this._soundButton.showResultSoundButton();

        this.setTimeout(callback);
    }

    hideBrandIntro (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        [ this._brandIntroScene ].invoke(MODES.HIDE);

        this._soundButton.hideResultSoundButton();

        this.setTimeout(() => {
            callback();
            this._layerManager.hideLayer(LAYERS.BRAND_INTRO);
        }, 1000);
    }

    showBrandQuestion (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        this._layerManager.showLayer(LAYERS.BRAND_QUESTION);

        const group = [
            [ this._brandQuestionScene, 0 ],
        ];

        group.invoke(MODES.SHOW);

        this._soundButton.showResultSoundButton();

        this.setTimeout(callback);
    }

    hideBrandQuestion (callback: () => void) {
        const { LAYERS, MODES } = this._config.options;
        [ this._brandQuestionScene ].invoke(MODES.HIDE);

        this._soundButton.hideResultSoundButton();

        this.setTimeout(() => {
            callback();
            this._layerManager.hideLayer(LAYERS.BRAND_QUESTION);
        }, 1000);
    }
}
