import { DisplacementFactory } from './displacement.factory';
import { Config } from '../config';


export class PreloadFactory {
    constructor (
        private readonly _config: Config,
        private readonly _displacementFactory: DisplacementFactory
    ) {
        this.URLs.forEach(this.create);
    }

    private create = (imageSrc: string) => {
        const { LAYERS } = this._config.options;

        this._displacementFactory.create({
            layer: LAYERS.PRELOAD,
            imageSrc,
            width: 0,
            height: 0,
            startX: 0,
            startY: 0,
            endX: 0,
            endY: 0,
            animMap: {},
            duration: 0,
        }).set();
    }

    private get URLs () {
        const { FRUIT_CONFIG, LOSE_CONFIG } = this._config.options;

        return [
            Object.values(FRUIT_CONFIG.infos).map(({ image, cutImages: [ cutImage1, cutImage2 ] }) => {
                return [ image, cutImage1, cutImage2 ];
            }).flat(),
            FRUIT_CONFIG.shadowImage,
            LOSE_CONFIG.conf.map(({ src }) => src),
        ].flat().filter(Boolean);
    }
}
