import timeline from '../scripts/timeline';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class FPS {
    text: any;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    set () {
        const { IS_DEBUG, LAYERS, FPS_CONFIG } = this._config.options;

        if (!IS_DEBUG) return;

        this.text = this._layerManager.createText(
            LAYERS.DEFAULT,
            FPS_CONFIG.prefix + '0',
            FPS_CONFIG.x,
            FPS_CONFIG.y,
        ).attr('fill', FPS_CONFIG.fill);
    }

    update = () => {
        const { IS_DEBUG, FPS_CONFIG } = this._config.options;

        if (!IS_DEBUG) return;

        this.text.attr('text', FPS_CONFIG.prefix + (timeline.getFPS() >> 0));
    };
}
