import Ucren from '../libs/ucren';
import message from './message';
import { Knifes } from '../object/knifes';
import { viewportSize } from '../layout/viewport';
import { Config } from '../config';


export class Control {
    canvasTop  = 0;
    canvasLeft = 0;

    constructor (
        private readonly _config: Config,
        private readonly _knifes: Knifes
    ) {}

    init () {
        this.fixCanvasPos();
        this.installDragger();
    }

    fix = () => {
        const { viewportWidth, viewportHeight } = viewportSize;

        this.canvasLeft = (document.documentElement.clientWidth - viewportWidth) / 2;
        this.canvasTop  = (document.documentElement.clientHeight - viewportHeight) / 2;
    };

    fixCanvasPos () {
        this.fix();
        Ucren.addEvent(window, 'resize', this.fix);
    }

    dragHandler = (dx: number, dy: number, x: number, y: number, kf: any) => {
        const { MESSAGES, INTERACTION_MODE, INTERACTION_MODES } = this._config.options;

        const posX = x - this.canvasLeft;
        const posY = y - this.canvasTop;

        if (INTERACTION_MODE === INTERACTION_MODES.SLICE) {
            const throughResult = this._knifes.through(posX, posY);
            if (throughResult) message.postMessage(MESSAGES.SLICE, throughResult);
        } else if (INTERACTION_MODE === INTERACTION_MODES.TOUCH) {
            message.postMessage(MESSAGES.TOUCH, [ posX, posY ]);
        }
    };

    startDragHandler = () => {
        this._knifes.newKnife();
    };

    installDragger () {
        // @ts-ignore
        const dragger = new Ucren.BasicDrag({ type: 'calc' });

        dragger.on('returnValue', this.dragHandler);

        dragger.on('startDrag', this.startDragHandler);

        dragger.bind(document.documentElement);
    }
}
