import { Config } from '../config';
import tween from '../libs/tween';
import { LayerManager } from '../managers/layer.manager';
import vibration from '../object/vibration';
import message from '../scripts/message';
import timeline from '../scripts/timeline';


export class HomeScene {
    logo: any;
    mask: any;
    text1: any;
    text2: any;
    text3: any;
    button: any;
    buttonText: any;
    _group: any;
    anims = [];
    anim  = tween.exponential.co;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    set () {
        const { LAYERS, HOME_LOGO_CONFIG, HOME_MASK_CONFIG, HOME_TEXT_CONFIG, START_BUTTON_CONFIG, MESSAGES } = this._config.options;

        HOME_LOGO_CONFIG.image && (this.logo = this._layerManager.createImage(
            LAYERS.START_SCREEN,
            HOME_LOGO_CONFIG.image,
            HOME_LOGO_CONFIG.startX,
            HOME_LOGO_CONFIG.startY,
            HOME_LOGO_CONFIG.width,
            HOME_LOGO_CONFIG.height,
        ).hide());

        HOME_MASK_CONFIG.image && (this.mask = this._layerManager.createImage(
            LAYERS.START_SCREEN,
            HOME_MASK_CONFIG.image,
            HOME_MASK_CONFIG.startX,
            HOME_MASK_CONFIG.startY,
            HOME_MASK_CONFIG.width,
            HOME_MASK_CONFIG.height,
        ).hide());

        HOME_TEXT_CONFIG.text1Value && (this.text1 = this._layerManager.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text1Value,
            HOME_TEXT_CONFIG.text1Sx,
            HOME_TEXT_CONFIG.text1Y,
            HOME_TEXT_CONFIG.text1Fill,
            HOME_TEXT_CONFIG.text1Size,
            HOME_TEXT_CONFIG.text1Weight,
            HOME_TEXT_CONFIG.text1Anchor,
        ).hide());

        HOME_TEXT_CONFIG.text2Value && (this.text2 = this._layerManager.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text2Value,
            HOME_TEXT_CONFIG.text2Sx,
            HOME_TEXT_CONFIG.text2Y,
            HOME_TEXT_CONFIG.text2Fill,
            HOME_TEXT_CONFIG.text2Size,
            HOME_TEXT_CONFIG.text2Weight,
            HOME_TEXT_CONFIG.text2Anchor,
        ).hide());

        HOME_TEXT_CONFIG.text3Value && (this.text3 = this._layerManager.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text3Value,
            HOME_TEXT_CONFIG.text3Sx,
            HOME_TEXT_CONFIG.text3Y,
            HOME_TEXT_CONFIG.text3Fill,
            HOME_TEXT_CONFIG.text3Size,
            HOME_TEXT_CONFIG.text3Weight,
            HOME_TEXT_CONFIG.text3Anchor,
        ).hide());

        this.button = this._layerManager.createImage(
            LAYERS.START_SCREEN,
            START_BUTTON_CONFIG.image,
            START_BUTTON_CONFIG.startX,
            START_BUTTON_CONFIG.startY,
            START_BUTTON_CONFIG.width,
            START_BUTTON_CONFIG.height,
        ).hide();

        this.buttonText = this._layerManager.createText(
            LAYERS.START_SCREEN,
            START_BUTTON_CONFIG.textValue,
            START_BUTTON_CONFIG.textStartX,
            START_BUTTON_CONFIG.textStartY,
            START_BUTTON_CONFIG.textFill,
            START_BUTTON_CONFIG.textSize,
            START_BUTTON_CONFIG.textWeight,
            START_BUTTON_CONFIG.textAnchor,
        );

        this._group = this._layerManager.getLayer(LAYERS.START_SCREEN).set();
        this._group.push(this.button, this.buttonText);

        this._group.click(() => {
            message.postMessage(MESSAGES.START_GAME);
            vibration.vibrate();
        });

        this._layerManager.hideLayer(LAYERS.START_SCREEN);
    }

    show (start: number) {
        const { MODES, HOME_LOGO_CONFIG, HOME_TEXT_CONFIG, START_BUTTON_CONFIG } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : HOME_LOGO_CONFIG.duration,
            data        : [
                MODES.SHOW,
                HOME_LOGO_CONFIG.startY,
                HOME_LOGO_CONFIG.endY,
                HOME_TEXT_CONFIG.text1Sx,
                HOME_TEXT_CONFIG.text1Ex,
                HOME_TEXT_CONFIG.text2Sx,
                HOME_TEXT_CONFIG.text2Ex,
                HOME_TEXT_CONFIG.text3Sx,
                HOME_TEXT_CONFIG.text3Ex,
                START_BUTTON_CONFIG.startY,
                START_BUTTON_CONFIG.endY,
                START_BUTTON_CONFIG.textStartY,
                START_BUTTON_CONFIG.textEndY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start: number) {
        const { MODES, HOME_LOGO_CONFIG, HOME_TEXT_CONFIG, START_BUTTON_CONFIG } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : HOME_LOGO_CONFIG.duration,
            data        : [
                MODES.SHOW,
                HOME_LOGO_CONFIG.endY,
                HOME_LOGO_CONFIG.startY,
                HOME_TEXT_CONFIG.text1Ex,
                HOME_TEXT_CONFIG.text1Sx,
                HOME_TEXT_CONFIG.text2Ex,
                HOME_TEXT_CONFIG.text2Sx,
                HOME_TEXT_CONFIG.text3Ex,
                HOME_TEXT_CONFIG.text3Sx,
                START_BUTTON_CONFIG.endY,
                START_BUTTON_CONFIG.startY,
                START_BUTTON_CONFIG.textEndY,
                START_BUTTON_CONFIG.textStartY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (
        time: number,
        mode: Mode,
        lsy: number,
        ley: number,
        t1sx: number,
        t1ex: number,
        t2sx: number,
        t2ex: number,
        t3sx: number,
        t3ex: number,
        bsy: number,
        bey: number,
        btsy: number,
        btey: number,
    ) {
        const { HOME_LOGO_CONFIG, HOME_TEXT_CONFIG } = this._config.options;

        this.logo && this.logo.attr('y', this.anim(time, lsy, ley - lsy, HOME_LOGO_CONFIG.duration));
        this.text1 && this.text1.attr('x', this.anim(time, t1sx, t1ex - t1sx, HOME_TEXT_CONFIG.animLength));
        this.text2 && this.text2.attr('x', this.anim(time, t2sx, t2ex - t2sx, HOME_TEXT_CONFIG.animLength));
        this.text3 && this.text3.attr('x', this.anim(time, t3sx, t3ex - t3sx, HOME_TEXT_CONFIG.animLength));
        this.button.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.buttonText.attr('y', this.anim(time, btsy, btey - btsy, HOME_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode: Mode) {
        const { MODES } = this._config.options;
        const group = [ this.button, this.buttonText ];

        if (this.logo) group.push(this.logo);
        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);

        if (mode === MODES.SHOW) {
            group.invoke(mode);
        }
    }

    onTimeEnd (mode: Mode) {
        const { MODES } = this._config.options;
        const group = [ this.button, this.buttonText ];

        if (this.logo) group.push(this.logo);
        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);

        if (mode === MODES.HIDE) {
            group.invoke(mode);
        }
    }
}
