import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';
import { Juice } from '../object/juice';


export class JuiceFactory {
    num: number;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {
        const { JUICE_CONFIG } = this._config.options;

        this.num = JUICE_CONFIG.number;
    }


    create (x: number, y: number, color: string | null) {
        for (let i = 0; i < this.num; i++) {
            this.createOne(x, y, color);
        }
    }

    private createOne (x: number, y: number, color: string | null) {
        if (!color) return;

        const { JUICE_CONFIG } = this._config.options;
        const juice = new Juice(this._config, this._layerManager, x, y, color, JUICE_CONFIG.shape);
        juice.render();
        juice.sputter();
    }
}
