import { Config } from '../config';
import { calcSize } from '../layout/calc-size';
import { viewportSize } from '../layout/viewport';
import { Control } from '../scripts/control';
import { LayerManager } from '../managers/layer.manager';
import tween from '../libs/tween';
import message from '../scripts/message';
import timeline from '../scripts/timeline';
import { average } from '../scripts/tools';
import { randomNumber } from '../scripts/utils';
import { FruitFactory } from '../factories/fruit.factory';
import { JuiceFactory } from '../factories/juice.factory';
import { Flash } from './flash';


export class Fruit {
    _juiceFactory: JuiceFactory;

    image: any;
    shadow: any;
    zoomAnim                       = tween.exponential.co;
    linearAnim                     = tween.linear;
    dropAnim                       = tween.quadratic.ci;
    fallOffAnim                    = tween.quadratic.co;
    rotateSpeed: number | number[] = [ 60, 50, 40, -40, -50, -60 ];
    brokend                        = false;
    apartAngle                     = 0;
    aparted                        = false;
    shotOutStartX: number          = 0;
    shotOutStartY: number          = 0;
    shotOutEndX: number            = 0;
    shotOutEndY: number            = 0;
    fallOffToX: number             = 0;
    fallTargetX: number            = 0;
    fallTargetY: number            = 0;
    touchedPosX: number            = 0;
    touchedPosY: number            = 0;
    brokenPosX: number             = 0;
    brokenPosY: number             = 0;
    brokenTargetX1: number         = 0;
    brokenTargetY1: number         = 0;
    brokenTargetX2: number         = 0;
    brokenTargetY2: number         = 0;
    touchedTargetX: number         = 0;
    touchedTargetY: number         = 0;
    bImage1RotateAngle: number     = 0;
    bImage2RotateAngle: number     = 0;
    fallOutOfViewerCalled          = 0;
    fallOffing                     = 0;
    type: FruitType;
    shotOut                        = (() => {
        const sign             = [ -1, 1 ];
        const { FRUIT_CONFIG } = this._config.options;

        return (start: number, endX: number) => {
            this.shotOutStartX = this.originX;
            this.shotOutStartY = this.originY;
            this.shotOutEndX   = average(this.originX, endX);
            this.shotOutEndY   = Math.min(this.startY - randomNumber(this.startY - 100), 200);
            this.fallOffToX    = endX;

            timeline.createTask({
                start       : start,
                duration    : FRUIT_CONFIG.dropTime,
                object      : this,
                onTimeUpdate: this.onShotOuting,
                onTimeStart : this.onShotOutStart,
                onTimeEnd   : this.onShotOutEnd,
                recycle     : this.anims,
            });

            if (this.type !== FRUIT_CONFIG.types.BOOM) {
                this.rotate(0, (randomNumber(180) + 90) * sign[randomNumber(2)]);
            }

            return this;
        };
    })();
    fallOff                        = (() => {
        const sign             = [ -1, 1 ];
        const { FRUIT_CONFIG } = this._config.options;
        let signIndex          = 0;

        return (start: number, x?: number) => {
            if (this.aparted || this.brokend) return;

            const { viewportHeight } = viewportSize;

            this.fallOffing = 1;

            const y = viewportHeight + calcSize(120);

            if (typeof x !== 'number') {
                x = this.originX + randomNumber(FRUIT_CONFIG.dropXScope) * sign[(signIndex++) % 2];
            }

            this.fallTargetX = x;
            this.fallTargetY = y;

            timeline.createTask({
                start       : start,
                duration    : FRUIT_CONFIG.dropTime,
                object      : this,
                onTimeUpdate: this.onFalling,
                onTimeStart : this.onFallStart,
                onTimeEnd   : this.onFallEnd,
                recycle     : this.anims,
            });
        };
    })();
    originX: number;
    originY: number;
    radius: number;
    startX: number;
    startY: number;
    anims: any[];
    debugArea: any;
    bImage1: any;
    bImage2: any;

    constructor (
        private readonly _config: Config,
        private readonly _control: Control,
        private readonly _layerManager: LayerManager,
        private readonly _flash: Flash,
        private readonly conf: any,
    ) {
        this._config       = _config;
        this._control      = _control;
        this._layerManager = _layerManager;
        this._juiceFactory = new JuiceFactory(_config, _layerManager);
        this._flash        = _flash;

        const { FRUIT_CONFIG } = this._config.options;

        this.type    = this.conf.type;
        this.originX = this.conf.originX;
        this.originY = this.conf.originY;

        const { radius } = FRUIT_CONFIG.infos[this.type];

        this.radius = radius;
        this.startX = this.conf.originX;
        this.startY = this.conf.originY;
        this.anims  = [];
    }

    set (hide?: boolean) {
        const { IS_DEBUG, IS_TOUCH, LAYERS, FRUIT_CONFIG } = this._config.options;
        const { radius, image, width, height, fixAngle }   = FRUIT_CONFIG.infos[this.type];

        this.shadow = this._layerManager.createImage(
            LAYERS.FRUIT,
            FRUIT_CONFIG.shadowImage,
            this.startX - radius,
            this.startY - radius + FRUIT_CONFIG.shadowPos,
            106,
            77,
        );

        this.image = this._layerManager.createImage(
            LAYERS.FRUIT,
            image,
            this.startX - radius,
            this.startY - radius,
            width,
            height,
        );

        IS_TOUCH && this.addClickHandler();

        if (this.type === FRUIT_CONFIG.types.BOOM) {
            this.image.rotate(fixAngle);
        }

        if (hide) {
            this.image.hide();
            this.shadow.hide();
        }

        IS_DEBUG && this.showDebugCatchArea();

        return this;
    }

    clickHandler = (event: MouseEvent) => {
        const { MESSAGES } = this._config.options;
        const posX         = event.clientX - this._control.canvasLeft;
        const posY         = event.clientY - this._control.canvasTop;
        message.postMessage(MESSAGES.TOUCH, [ posX, posY ]);
    };

    addClickHandler () {
        this.image.node.addEventListener('pointerdown', this.clickHandler);
    }

    removeClickHandler () {
        this.image.node.removeEventListener('pointerdown', this.clickHandler);
    }

    showDebugCatchArea () {
        const { originX, originY, radius } = this;

        this.debugArea = this.image.paper.circle(originX, originY, radius).attr({
            stroke            : 'mediumblue',
            'stroke-width'    : 2,
            'stroke-dasharray': '- ',
            opacity           : 0.5,
        });

        this.debugArea.insertBefore(this.image);
    }

    pos (x: number, y: number) {
        const { IS_DEBUG, MESSAGES, FRUIT_CONFIG } = this._config.options;
        const { viewportHeight }                   = viewportSize;
        const r                                    = this.radius;
        this.originX                               = x;
        this.originY                               = y;

        this.image.attr({ x: x -= r, y: y -= r });
        this.shadow.attr({ x: x, y: y + FRUIT_CONFIG.shadowPos });
        IS_DEBUG && this.debugArea.attr({ cx: x + r, cy: y + r });

        if (this.fallOffing && !this.fallOutOfViewerCalled && y > viewportHeight + this.radius) {
            this.fallOutOfViewerCalled = 1;
            message.postMessage(MESSAGES.FRUIT_FALL_OUT_OF_VIEWER, this);
        }
    }

    show (start: number) {
        const { MODES } = this._config.options;
        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1e-5, 1, MODES.SHOW ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeStart : this.onShowStart,
            recycle     : this.anims,
        });
    }

    hide (start?: number) {
        const { MODES, FRUIT_CONFIG } = this._config.options;
        if (this.type !== FRUIT_CONFIG.types.BOOM) return;

        // @ts-ignore
        this.anims.clear && this.anims.clear();

        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1, 1e-5, MODES.HIDE ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeEnd   : this.onHideEnd,
            recycle     : this.anims,
        });
    }

    rotate (start: number, speed: number) {
        this.rotateSpeed = speed || Array.isArray(this.rotateSpeed)
            // @ts-ignore
            ? this.rotateSpeed[randomNumber(this.rotateSpeed.length)]
            : this.rotateSpeed;
        timeline.createTask({
            start       : start,
            duration    : -1,
            object      : this,
            onTimeUpdate: this.onRotating,
            recycle     : this.anims,
        });
    }

    broken (angle: number) {
        if (this.brokend) return;

        this.brokend = true;

        const { IS_TOUCH, FRUIT_CONFIG } = this._config.options;
        const index                      = FruitFactory.fruitCache.indexOf(this);
        if (index > -1) FruitFactory.fruitCache.splice(index, 1);

        if (this.type !== FRUIT_CONFIG.types.BOOM) {
            this._flash.showAt(this.originX, this.originY, angle);
            this._juiceFactory.create(this.originX, this.originY, FRUIT_CONFIG.infos[this.type].juiceColor);

            IS_TOUCH
                ? this.touched()
                : this.apart(angle);
        } else {
            this.hide();
        }
    }

    pause () {
        if (this.brokend) return;

        // @ts-ignore
        this.anims.clear && this.anims.clear();
    }

    touched () {
        const { MODES, FRUIT_CONFIG } = this._config.options;
        // @ts-ignore
        this.anims.clear && this.anims.clear();
        this.shadow.hide();

        timeline.createTask({
            start       : 0,
            duration    : FRUIT_CONFIG.dropTime,
            data        : [ 1, 1e-5, MODES.HIDE, 1500 ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeStart : this.onTouchedDropStart,
            onTimeEnd   : this.onTouchedDropEnd,
            recycle     : this.anims,
        });

        this.debugArea && this.debugArea.remove();
    }

    apart (angle: number) {
        const { LAYERS, FRUIT_CONFIG } = this._config.options;
        // @ts-ignore
        this.anims.clear && this.anims.clear();
        this.image.hide();
        this.shadow.hide();
        this.aparted = true;

        const inf = FRUIT_CONFIG.infos[this.type];

        const { cutImages, width, height, radius, fixAngle } = inf;

        // @ts-ignore
        const create = this._layerManager.createImage.saturate(
            this._layerManager,
            this.startX - radius,
            this.startY - radius,
            width,
            height,
        );

        angle = ((angle % 180) + 360 + fixAngle) % 360;

        this.bImage1 = create(LAYERS.FRUIT, cutImages[0]);
        this.bImage2 = create(LAYERS.FRUIT, cutImages[1]);

        [ this.bImage1, this.bImage2 ].invoke('rotate', angle);

        this.apartAngle = angle;

        timeline.createTask({
            start       : 0,
            duration    : FRUIT_CONFIG.dropTime,
            object      : this,
            onTimeUpdate: this.onBrokenDropUpdate,
            onTimeStart : this.onBrokenDropStart,
            onTimeEnd   : this.onBrokenDropEnd,
            recycle     : this.anims,
        });

        this.debugArea && this.debugArea.remove();
    }

    remove () {
        const { IS_TOUCH, MESSAGES } = this._config.options;
        IS_TOUCH && this.removeClickHandler();

        // @ts-ignore
        this.anims.clear && this.anims.clear();

        if (this.image) {
            this.image.remove();
            this.shadow.remove();
            this.debugArea && this.debugArea.remove();
        }

        if (this.bImage1) {
            this.bImage1.remove();
            this.bImage2.remove();
        }

        const index = FruitFactory.fruitCache.indexOf(this);
        if (index > -1) FruitFactory.fruitCache.splice(index, 1);

        for (const name in this) {
            if (typeof this[name] === 'function') {
                // @ts-ignore
                this[name] = function (name) {
                    return function () {
                        throw new Error('method ' + name + ' has been removed');
                    };
                }(name);
            } else {
                delete this[name];
            }
        }

        message.postMessage(MESSAGES.FRUIT_REMOVE, this);
    }

    onShowStart () {
        this.image.show();
        this.shadow.show();
    }

    onScaling (time: number, a: number, b: number, z: number, animationTime = 500) {
        this.image.scale(z = this.zoomAnim(time, a, b - a, animationTime), z);
        this.shadow.scale(z, z);
    }

    onHideEnd () {
        this.remove();
    }

    onRotating (time: number) {
        const speed = Array.isArray(this.rotateSpeed)
            ? this.rotateSpeed[randomNumber(this.rotateSpeed.length)]
            : this.rotateSpeed;
        this.image.rotate((speed * time / 1e3) % 360, true);
    }

    onTouchedDropStart () {
        const { viewportHeight, viewportWidth } = viewportSize;

        this.touchedTargetX = randomNumber(viewportWidth) - 175;
        this.touchedTargetY = viewportHeight;
        this.touchedPosX    = this.originX;
        this.touchedPosY    = this.originY;

        for (let i = FruitFactory.fruitCache.length - 1; i >= 0; i--) {
            if (FruitFactory.fruitCache[i] === this) {
                FruitFactory.fruitCache.splice(i, 1);
            }
        }
    }

    onTouchedDropEnd () {
        this.remove();
    }

    onBrokenDropUpdate (time: number) {
        const { FRUIT_CONFIG } = this._config.options;
        const radius           = this.radius;
        this.bImage1.attr({
            x: this.linearAnim(time, this.brokenPosX - radius, this.brokenTargetX1, FRUIT_CONFIG.dropTime),
            y: this.dropAnim(time, this.brokenPosY - radius, this.brokenTargetY1 - this.brokenPosY + radius, FRUIT_CONFIG.dropTime),
        }).rotate(this.linearAnim(time, this.apartAngle, this.bImage1RotateAngle, FRUIT_CONFIG.dropTime), true);
        this.bImage2.attr({
            x: this.linearAnim(time, this.brokenPosX - radius, this.brokenTargetX2, FRUIT_CONFIG.dropTime),
            y: this.dropAnim(time, this.brokenPosY - radius, this.brokenTargetY2 - this.brokenPosY + radius, FRUIT_CONFIG.dropTime),
        }).rotate(this.linearAnim(time, this.apartAngle, this.bImage2RotateAngle, FRUIT_CONFIG.dropTime), true);
    }

    onBrokenDropStart () {
        const { FRUIT_CONFIG }   = this._config.options;
        const { viewportHeight } = viewportSize;
        this.brokenTargetX1      = -(randomNumber(FRUIT_CONFIG.dropXScope) + 75);
        this.brokenTargetX2      = randomNumber(FRUIT_CONFIG.dropXScope + 75);
        this.brokenTargetY1      = viewportHeight;
        this.brokenTargetY2      = viewportHeight;
        this.brokenPosX          = this.originX;
        this.brokenPosY          = this.originY;
        this.bImage1RotateAngle  = -randomNumber(150) - 50;
        this.bImage2RotateAngle  = randomNumber(150) + 50;

        for (let i = FruitFactory.fruitCache.length - 1; i >= 0; i--) {
            if (FruitFactory.fruitCache[i] === this) {
                FruitFactory.fruitCache.splice(i, 1);
            }
        }
    }

    onBrokenDropEnd () {
        this.remove();
    }

    onShotOuting (time: number) {
        const { FRUIT_CONFIG } = this._config.options;
        this.pos(
            this.linearAnim(time, this.shotOutStartX, this.shotOutEndX - this.shotOutStartX, FRUIT_CONFIG.dropTime),
            this.fallOffAnim(time, this.shotOutStartY, this.shotOutEndY - this.shotOutStartY, FRUIT_CONFIG.dropTime),
        );
    }

    onShotOutStart () {
    }

    onShotOutEnd () {
        this.fallOff(0, this.fallOffToX);
    }

    onFalling (time: number) {
        const { FRUIT_CONFIG } = this._config.options;
        this.pos(
            this.linearAnim(time, this.brokenPosX, this.fallTargetX - this.brokenPosX, FRUIT_CONFIG.dropTime),
            this.dropAnim(time, this.brokenPosY, this.fallTargetY - this.brokenPosY, FRUIT_CONFIG.dropTime),
        );
    }

    onFallStart () {
        this.brokenPosX = this.originX;
        this.brokenPosY = this.originY;
    }

    onFallEnd () {
        const { MESSAGES } = this._config.options;
        message.postMessage(MESSAGES.FRUIT_FALL_OFF, this);
        this.remove();
    }
}
