import { BASE_RESULT_CHARS_NUMBER, HORIZONTAL_BASE, VERTICAL_BASE } from '../config/constants';
import { viewportSize } from './viewport';


export const calcSize = (size: number | string) => {
    const { viewportWidth } = viewportSize;

    if (typeof size === 'string') size = Number(size);

    const ratio = viewportWidth / HORIZONTAL_BASE;
    return size * ratio;
};

export const calcVerticalSize = (size: number | string) => {
    const { viewportHeight } = viewportSize;

    if (typeof size === 'string') size = Number(size);

    const ratio = viewportHeight / VERTICAL_BASE;
    return size * ratio;
};

export const calcResultFontSize = (string: string, base: number) => {
    const fontSize     = calcSize(base);
    const stringLength = string.length;
    const charsNumber  = stringLength > BASE_RESULT_CHARS_NUMBER ? stringLength : BASE_RESULT_CHARS_NUMBER;
    return fontSize * BASE_RESULT_CHARS_NUMBER / charsNumber;
};
