import { nul } from './utils';


let addingTasks = [];
let adding      = 0;
let tasks       = [];


class Timeline {
    _requestAnimationFrame = function (glob) {
        return glob.requestAnimationFrame ||
            glob.mozRequestAnimationFrame ||
            glob.webkitRequestAnimationFrame ||
            glob.msRequestAnimationFrame ||
            glob.oRequestAnimationFrame || function (callback) {
                setTimeout(callback, 1);
            };
    }(window);

    constructor () {
        this.startTime = 0;
        this.count     = 0;
    }

    now () {
        return new Date().getTime();
    }

    init () {
        this.startTime = this.now();
        this.count     = 0;

        const time = 1;

        setInterval(() => {
            this.count++;
            this.update(this.now());
        }, time);
    }

    createTask (conf) {
        /* e.g. createTask({
            start: 500, duration: 2000, data: [a, b, c,..],
            object: module, onTimeUpdate: fn(time, a, b, c,..), onTimeStart: fn(a, b, c,..), onTimeEnd: fn(a, b, c,..),
            recycle: []
        }); */
        const task = this._createTask(conf);
        addingTasks.unshift(task);
        adding = 1;

        if (conf.recycle)
            this.taskList(conf.recycle, task);

        return task;
    }

    taskList (queue, task) {
        if (!queue.clear)
            queue.clear = function () {
                for (let task, i = this.length - 1; i >= 0; i--) {
                    task = this[i];
                    task.stop();
                    this.splice(i, 1);
                }
                return this;
            };

        if (task)
            queue.unshift(task);

        return queue;
    }

    setTimeout (fn, time) {
        // e.g. setTimeout(fn, time);
        return this.createTask({ start: time, duration: 0, onTimeStart: fn });
    }

    _setInterval (fn, time) {
        // e.g. setInterval(fn, time);
        const timer = setInterval(fn, time);
        return {
            stop: function () {
                clearInterval(timer);
            },
        };
    }

    getFPS () {
        const t = this.now(), fps = this.count / (t - this.startTime) * 1e3;
        if (this.count > 1e3) {
            this.count     = 0;
            this.startTime = t;
        }
        return fps;
    }

    _createTask (conf) {
        const object = conf.object || {};
        conf.start   = conf.start || 0;
        return {
            start       : conf.start + this.now(),
            duration    : conf.duration === -1 ? 86400000 : conf.duration,
            data        : conf.data ? [ 0 ].concat(conf.data) : [ 0 ],
            started     : 0,
            object      : object,
            onTimeStart : conf.onTimeStart || object.onTimeStart || nul,
            onTimeUpdate: conf.onTimeUpdate || object.onTimeUpdate || nul,
            onTimeEnd   : conf.onTimeEnd || object.onTimeEnd || nul,
            stop        : function () {
                this.stopped = 1;
            },
        }
    }

    updateTask (task, time) {
        const data = task.data;
        data[0]    = time;
        task.onTimeUpdate.apply(task.object, data);
    }

    checkStartTask (task) {
        if (!task.started) {
            task.started = 1;
            task.onTimeStart.apply(task.object, task.data.slice(1));
            this.updateTask(task, 0);
        }
    }

    update (time) {
        let i = tasks.length, t, task, start, duration;


        while (i--) {
            task     = tasks[i];
            start    = task.start;
            duration = task.duration;

            if (time >= start) {

                if (task.stopped) {
                    tasks.splice(i, 1);
                    continue;
                }

                this.checkStartTask(task);
                if ((t = time - start) < duration) {
                    this.updateTask(task, t);
                } else {
                    this.updateTask(task, duration);
                    task.onTimeEnd.apply(task.object, task.data.slice(1));
                    tasks.splice(i, 1);
                }
            }
        }

        if (adding) {
            tasks.unshift.apply(tasks, addingTasks);
            addingTasks.length = adding = 0;
        }
    }
}


export default new Timeline();
