import timeline from '../scripts/timeline';
import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';


export type DisplacementConfig = {
    layer: LayerName;
    imageSrc: string;
    width: number;
    height: number;
    startX: number;
    startY: number;
    endX: number;
    endY: number;
    animMap: any;
    duration: number;
}


export class Displacement {
    image: any;
    layer: LayerName;
    imageSrc: string;
    width: number;
    height: number;
    startX: number;
    startY: number;
    endX: number;
    endY: number;
    anim: any  = {};
    anims: any = [];
    duration: number;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
        { layer, imageSrc, width, height, startX, startY, endX, endY, animMap, duration }: DisplacementConfig,
    ) {
        this.layer    = layer;
        this.imageSrc = imageSrc;
        this.width    = width;
        this.height   = height;
        this.startX   = startX;
        this.startY   = startY;
        this.endX     = endX;
        this.endY     = endY;
        if (typeof animMap === 'function') {
            this.anim.show = this.anim.hide = animMap;
        } else {
            this.anim = animMap;
        }
        this.duration = duration;
    }

    createTask (start: number, duration: number, sx: number, sy: number, ex: number, ey: number, anim: number, mode: Mode) {
        timeline.createTask({
            start       : start,
            duration    : duration,
            object      : this,
            data        : [ sx, sy, ex, ey, anim, mode ],
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    set () {
        this.image = this._layerManager.createImage(
            this.layer,
            this.imageSrc,
            this.startX,
            this.startY,
            this.width,
            this.height,
        );
    }

    show (start: number = 0) {
        const { MODES } = this._config.options;
        this.createTask(
            start,
            this.duration,
            this.startX,
            this.startY,
            this.endX,
            this.endY,
            this.anim.show,
            MODES.SHOW,
        );
    }

    hide () {
        const { MODES } = this._config.options;
        this.anims.clear();
        this.createTask(
            0,
            this.duration,
            this.endX,
            this.endY,
            this.startX,
            this.startY,
            this.anim.hide,
            MODES.HIDE,
        );
    }

    onTimeUpdate (time: number, sx: number, sy: number, ex: number, ey: number, anim: any) {
        this.image.attr({
            x: anim(time, sx, ex - sx, this.duration),
            y: anim(time, sy, ey - sy, this.duration),
        });
    }

    onTimeStart () {
    }

    onTimeEnd (sx: number, sy: number, ex: number, ey: number, mode: Mode) {
        const { MODES } = this._config.options;
        if (mode === MODES.HIDE) this.image.hide();
    }
}
