import tween from '../libs/tween';
import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';
import vibration from '../object/vibration';
import { Game } from '../scripts/game';
import message from '../scripts/message';
import timeline from '../scripts/timeline';


export class ResultScene {
    logo: any;
    text1: any;
    text2: any;
    text3: any;
    text4: any;
    text5: any;
    text6: any;
    text7: any;
    nextButton: any;
    nextButtonText: any;
    replayButton: any;
    replayButtonText: any;
    _group1: any;
    _group2: any;
    anims = [];
    anim  = tween.exponential.co;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
        private readonly _game: Game
    ) {}

    set () {
        const {
            LAYERS, RESULT_LOGO_CONFIG, RESULT_TEXT_CONFIG, RESULT_BUTTON_NEXT_CONFIG, MESSAGES, SCENES,
            RESULT_BUTTON_REPLAY_CONFIG,
        } = this._config.options;

        RESULT_LOGO_CONFIG.image && (this.logo = this._layerManager.createImage(
            LAYERS.RESULT_SCREEN,
            RESULT_LOGO_CONFIG.image,
            RESULT_LOGO_CONFIG.startX,
            RESULT_LOGO_CONFIG.startY,
            RESULT_LOGO_CONFIG.width,
            RESULT_LOGO_CONFIG.height,
        ).hide());

        RESULT_TEXT_CONFIG.text1Value && (this.text1 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text1Value,
            RESULT_TEXT_CONFIG.text1X,
            RESULT_TEXT_CONFIG.text1Sy,
            RESULT_TEXT_CONFIG.text1Fill,
            RESULT_TEXT_CONFIG.text1Size,
            RESULT_TEXT_CONFIG.text1Weight,
            RESULT_TEXT_CONFIG.text1Anchor,
        ).hide());

        RESULT_TEXT_CONFIG.text2Value && (this.text2 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text2Value,
            RESULT_TEXT_CONFIG.text2X,
            RESULT_TEXT_CONFIG.text2Sy,
            RESULT_TEXT_CONFIG.text2Fill,
            RESULT_TEXT_CONFIG.text2Size,
            RESULT_TEXT_CONFIG.text2Weight,
            RESULT_TEXT_CONFIG.text2Anchor,
        ).hide());

        this.text3 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            this._game.scoreNumber.toString(),
            RESULT_TEXT_CONFIG.text3X,
            RESULT_TEXT_CONFIG.text3Sy,
            RESULT_TEXT_CONFIG.text3Fill,
            RESULT_TEXT_CONFIG.text3Size,
            RESULT_TEXT_CONFIG.text3Weight,
            RESULT_TEXT_CONFIG.text3Anchor,
        )
            .attr('stroke', 'white')
            .attr('stroke-width', 3)
            .hide();


        RESULT_TEXT_CONFIG.text4Value && (this.text4 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text4Value,
            RESULT_TEXT_CONFIG.text4Sx,
            RESULT_TEXT_CONFIG.text4Y,
            RESULT_TEXT_CONFIG.text4Fill,
            RESULT_TEXT_CONFIG.text4Size,
            RESULT_TEXT_CONFIG.text4Weight,
            RESULT_TEXT_CONFIG.text4Anchor,
        ).hide());

        RESULT_TEXT_CONFIG.text5Value && (this.text5 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text5Value,
            RESULT_TEXT_CONFIG.text5Sx,
            RESULT_TEXT_CONFIG.text5Y,
            RESULT_TEXT_CONFIG.text5Fill,
            RESULT_TEXT_CONFIG.text5Size,
            RESULT_TEXT_CONFIG.text5Weight,
            RESULT_TEXT_CONFIG.text5Anchor,
        ).attr('stroke', 'white')
            .attr('stroke-width', 2)
            .hide());

        RESULT_TEXT_CONFIG.text6Value && (this.text6 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text6Value,
            RESULT_TEXT_CONFIG.text6Sx,
            RESULT_TEXT_CONFIG.text6Y,
            RESULT_TEXT_CONFIG.text6Fill,
            RESULT_TEXT_CONFIG.text6Size,
            RESULT_TEXT_CONFIG.text6Weight,
            RESULT_TEXT_CONFIG.text6Anchor,
        ).hide());

        RESULT_TEXT_CONFIG.text7Value && (this.text7 = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text7Value,
            RESULT_TEXT_CONFIG.text7Sx,
            RESULT_TEXT_CONFIG.text7Y,
            RESULT_TEXT_CONFIG.text7Fill,
            RESULT_TEXT_CONFIG.text7Size,
            RESULT_TEXT_CONFIG.text7Weight,
            RESULT_TEXT_CONFIG.text7Anchor,
        ).attr('stroke', 'white')
            .attr('stroke-width', 2)
            .hide());

        this.nextButton = this._layerManager.createImage(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_NEXT_CONFIG.image,
            RESULT_BUTTON_NEXT_CONFIG.startX,
            RESULT_BUTTON_NEXT_CONFIG.startY,
            RESULT_BUTTON_NEXT_CONFIG.width,
            RESULT_BUTTON_NEXT_CONFIG.height,
        ).hide();

        this.nextButtonText = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_NEXT_CONFIG.textValue,
            RESULT_BUTTON_NEXT_CONFIG.textStartX,
            RESULT_BUTTON_NEXT_CONFIG.textStartY,
            RESULT_BUTTON_NEXT_CONFIG.textFill,
            RESULT_BUTTON_NEXT_CONFIG.textSize,
            RESULT_BUTTON_NEXT_CONFIG.textWeight,
            RESULT_BUTTON_NEXT_CONFIG.textAnchor,
        );

        this.replayButton = this._layerManager.createImage(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_REPLAY_CONFIG.image,
            RESULT_BUTTON_REPLAY_CONFIG.startX,
            RESULT_BUTTON_REPLAY_CONFIG.startY,
            RESULT_BUTTON_REPLAY_CONFIG.width,
            RESULT_BUTTON_REPLAY_CONFIG.height,
        ).hide();

        this.replayButtonText = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_REPLAY_CONFIG.textValue,
            RESULT_BUTTON_REPLAY_CONFIG.textStartX,
            RESULT_BUTTON_REPLAY_CONFIG.textStartY,
            RESULT_BUTTON_REPLAY_CONFIG.textFill,
            RESULT_BUTTON_REPLAY_CONFIG.textSize,
            RESULT_BUTTON_REPLAY_CONFIG.textWeight,
            RESULT_BUTTON_REPLAY_CONFIG.textAnchor,
        );

        this._group1 = this._layerManager.getLayer(LAYERS.RESULT_SCREEN).set();
        this._group1.push(this.nextButton, this.nextButtonText);

        this._group1.click(() => {
            vibration.vibrate();
            message.postMessage(MESSAGES.SCENE_SWITCH_SCENE, SCENES.BRAND_INTRO);
        });

        this._group2 = this._layerManager.getLayer(LAYERS.RESULT_SCREEN).set();
        this._group2.push(this.replayButton, this.replayButtonText);

        this._group2.click(() => {
            message.postMessage(MESSAGES.START_GAME);
            vibration.vibrate();
        });

        this._layerManager.hideLayer(LAYERS.RESULT_SCREEN);
    }

    show (start: number) {
        const {
            MODES, RESULT_LOGO_CONFIG, RESULT_TEXT_CONFIG, RESULT_BUTTON_NEXT_CONFIG, RESULT_BUTTON_REPLAY_CONFIG,
        } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : RESULT_LOGO_CONFIG.duration,
            data        : [
                MODES.SHOW,
                RESULT_LOGO_CONFIG.startY,
                RESULT_LOGO_CONFIG.endY,
                RESULT_TEXT_CONFIG.text1Sy,
                RESULT_TEXT_CONFIG.text1Ey,
                RESULT_TEXT_CONFIG.text2Sy,
                RESULT_TEXT_CONFIG.text2Ey,
                RESULT_TEXT_CONFIG.text3Sy,
                RESULT_TEXT_CONFIG.text3Ey,
                RESULT_TEXT_CONFIG.text4Sx,
                RESULT_TEXT_CONFIG.text4Ex,
                RESULT_TEXT_CONFIG.text5Sx,
                RESULT_TEXT_CONFIG.text5Ex,
                RESULT_TEXT_CONFIG.text6Sx,
                RESULT_TEXT_CONFIG.text6Ex,
                RESULT_TEXT_CONFIG.text7Sx,
                RESULT_TEXT_CONFIG.text7Ex,
                RESULT_BUTTON_NEXT_CONFIG.startY,
                RESULT_BUTTON_NEXT_CONFIG.endY,
                RESULT_BUTTON_NEXT_CONFIG.textStartY,
                RESULT_BUTTON_NEXT_CONFIG.textEndY,
                RESULT_BUTTON_REPLAY_CONFIG.startY,
                RESULT_BUTTON_REPLAY_CONFIG.endY,
                RESULT_BUTTON_REPLAY_CONFIG.textStartY,
                RESULT_BUTTON_REPLAY_CONFIG.textEndY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start: number) {
        const {
            MODES, RESULT_LOGO_CONFIG, RESULT_TEXT_CONFIG, RESULT_BUTTON_NEXT_CONFIG, RESULT_BUTTON_REPLAY_CONFIG,
        } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : RESULT_LOGO_CONFIG.duration,
            data        : [
                MODES.SHOW,
                RESULT_LOGO_CONFIG.endY,
                RESULT_LOGO_CONFIG.startY,
                RESULT_TEXT_CONFIG.text1Ey,
                RESULT_TEXT_CONFIG.text1Sy,
                RESULT_TEXT_CONFIG.text2Ey,
                RESULT_TEXT_CONFIG.text2Sy,
                RESULT_TEXT_CONFIG.text3Ey,
                RESULT_TEXT_CONFIG.text3Sy,
                RESULT_TEXT_CONFIG.text4Ex,
                RESULT_TEXT_CONFIG.text4Sx,
                RESULT_TEXT_CONFIG.text5Ex,
                RESULT_TEXT_CONFIG.text5Sx,
                RESULT_TEXT_CONFIG.text6Ex,
                RESULT_TEXT_CONFIG.text6Sx,
                RESULT_TEXT_CONFIG.text7Ex,
                RESULT_TEXT_CONFIG.text7Sx,
                RESULT_BUTTON_NEXT_CONFIG.endY,
                RESULT_BUTTON_NEXT_CONFIG.startY,
                RESULT_BUTTON_NEXT_CONFIG.textEndY,
                RESULT_BUTTON_NEXT_CONFIG.textStartY,
                RESULT_BUTTON_REPLAY_CONFIG.endY,
                RESULT_BUTTON_REPLAY_CONFIG.startY,
                RESULT_BUTTON_REPLAY_CONFIG.textEndY,
                RESULT_BUTTON_REPLAY_CONFIG.textStartY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (
        time: number,
        mode: Mode,
        lsy: number,
        ley: number,
        t1sy: number,
        t1ey: number,
        t2sy: number,
        t2ey: number,
        t3sy: number,
        t3ey: number,
        t4sx: number,
        t4ex: number,
        t5sx: number,
        t5ex: number,
        t6sx: number,
        t6ex: number,
        t7sx: number,
        t7ex: number,
        nbsy: number,
        nbey: number,
        nbtsy: number,
        nbtey: number,
        rbsy: number,
        rbey: number,
        rbtsy: number,
        rbtey: number
    ) {
        const { RESULT_LOGO_CONFIG, RESULT_TEXT_CONFIG } = this._config.options;

        this.logo && this.logo.attr('y', this.anim(time, lsy, ley - lsy, RESULT_LOGO_CONFIG.duration));
        this.text1 && this.text1.attr('y', this.anim(time, t1sy, t1ey - t1sy, RESULT_TEXT_CONFIG.animLength));
        this.text2 && this.text2.attr('y', this.anim(time, t2sy, t2ey - t2sy, RESULT_TEXT_CONFIG.animLength));
        this.text3 && this.text3.attr('y', this.anim(time, t3sy, t3ey - t3sy, RESULT_TEXT_CONFIG.animLength));
        this.text4 && this.text4.attr('x', this.anim(time, t4sx, t4ex - t4sx, RESULT_TEXT_CONFIG.animLength));
        this.text5 && this.text5.attr('x', this.anim(time, t5sx, t5ex - t5sx, RESULT_TEXT_CONFIG.animLength));
        this.text6 && this.text6.attr('x', this.anim(time, t6sx, t6ex - t6sx, RESULT_TEXT_CONFIG.animLength));
        this.text7 && this.text7.attr('x', this.anim(time, t7sx, t7ex - t7sx, RESULT_TEXT_CONFIG.animLength));
        this.nextButton.attr('y', this.anim(time, nbsy, nbey - nbsy, RESULT_TEXT_CONFIG.animLength));
        this.nextButtonText.attr('y', this.anim(time, nbtsy, nbtey - nbtsy, RESULT_TEXT_CONFIG.animLength));
        this.replayButton.attr('y', this.anim(time, rbsy, rbey - rbsy, RESULT_TEXT_CONFIG.animLength));
        this.replayButtonText.attr('y', this.anim(time, rbtsy, rbtey - rbtsy, RESULT_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode: Mode) {
        const { MODES } = this._config.options;
        const group = [ this.nextButton, this.nextButtonText, this.replayButton, this.replayButtonText ];

        if (this.logo) group.push(this.logo);
        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);
        if (this.text4) group.push(this.text4);
        if (this.text5) group.push(this.text5);
        if (this.text6) group.push(this.text6);
        if (this.text7) group.push(this.text7);

        if (mode === MODES.SHOW) {
            group.invoke(mode);
        }
    }

    onTimeEnd (mode: Mode) {
        const { MODES } = this._config.options;
        const group = [ this.nextButton, this.nextButtonText, this.replayButton, this.replayButtonText ];

        if (this.logo) group.push(this.logo);
        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);
        if (this.text4) group.push(this.text4);
        if (this.text5) group.push(this.text5);
        if (this.text6) group.push(this.text6);
        if (this.text7) group.push(this.text7);

        if (mode === MODES.HIDE) {
            group.invoke(mode);
        }
    }
}
