import { SoundNames } from '../config/types';
import { Sound } from './sound';
import tween from '../libs/tween';
import timeline from '../scripts/timeline';
import { SoundFactory } from '../factories/sound.factory';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class Flash {
    image: any;
    snd: Sound;
    xDiff        = 0;
    yDiff        = 0;
    switchOn     = true;
    anim         = tween.quadratic.cio;
    anims: any[] = [];

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
        private readonly _soundFactory: SoundFactory,
    ) {
        const { SOUNDS } = this._config.options;
        this.snd         = this._soundFactory.create(SOUNDS[SoundNames.SPLATTER]);
    }

    set () {
        if (!this.switchOn) return false;

        const { LAYERS, FLASH_CONFIG } = this._config.options;

        this.image = this._layerManager.createImage(
            LAYERS.FLASH,
            FLASH_CONFIG.image,
            0,
            0,
            FLASH_CONFIG.width,
            FLASH_CONFIG.height,
        ).hide();
    }

    showAt (x: number, y: number, an: number) {
        const { FLASH_CONFIG } = this._config.options;

        if (!this.switchOn) return false;

        this.image && this.image.rotate(
            an,
            true,
        ).scale(
            1e-5,
            1e-5,
        ).attr({
            x: x + this.xDiff,
            y: y + this.yDiff,
        }).show();

        // @ts-ignore
        this.anims.clear && this.anims.clear();

        this.snd && this.snd.play();

        timeline.createTask({
            start       : 0,
            duration    : FLASH_CONFIG.duration,
            data        : [ 1e-5, 1 ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            recycle     : this.anims,
        });

        timeline.createTask({
            start       : FLASH_CONFIG.duration,
            duration    : FLASH_CONFIG.duration,
            data        : [ 1, 1e-5 ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time: number, a: number, b: number, z: number) {
        if (!this.switchOn) return false;

        const { FLASH_CONFIG } = this._config.options;

        this.image.scale(
            z = this.anim(
                time,
                a,
                b - a,
                FLASH_CONFIG.duration,
            ),
            z,
        );
    }
}
