import tween from '../libs/tween';
import message from '../scripts/message';
import timeline from '../scripts/timeline';
import vibration from './vibration';
import { NinjaConfig } from '../config/namespace';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


type Info = {
    image: any,
    config: NinjaConfig.LoseConf,
}


export class Lose {
    anim         = tween.exponential.co;
    back         = tween.back.co;
    o1: any;
    o2: any;
    o3: any;
    info: Info[] = [];
    animLength: number;
    number       = 0;
    anims        = [];

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {
        this.animLength = this._config.options.LOSE_CONFIG.animLength;
    }

    set () {
        const { LAYERS, LOSE_CONFIG } = this._config.options;

        this.o1 = this._layerManager.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[0].src,
            LOSE_CONFIG.conf[0].sx,
            LOSE_CONFIG.conf[0].y,
            LOSE_CONFIG.conf[0].w,
            LOSE_CONFIG.conf[0].h,
        ).hide();

        this.o2 = this._layerManager.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[1].src,
            LOSE_CONFIG.conf[1].sx,
            LOSE_CONFIG.conf[1].y,
            LOSE_CONFIG.conf[1].w,
            LOSE_CONFIG.conf[1].h,
        ).hide();

        this.o3 = this._layerManager.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[2].src,
            LOSE_CONFIG.conf[2].sx,
            LOSE_CONFIG.conf[2].y,
            LOSE_CONFIG.conf[2].w,
            LOSE_CONFIG.conf[2].h,
        ).hide();

        this.info = [
            { image: this.o1, config: LOSE_CONFIG.conf[0] },
            { image: this.o2, config: LOSE_CONFIG.conf[1] },
            { image: this.o3, config: LOSE_CONFIG.conf[2] },
        ];
    }

    show (start?: number) {
        const { LOSE_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : this.animLength,
            data        : [
                MODES.SHOW,
                LOSE_CONFIG.conf[0].sx,
                LOSE_CONFIG.conf[0].ex,
                LOSE_CONFIG.conf[1].sx,
                LOSE_CONFIG.conf[1].ex,
                LOSE_CONFIG.conf[2].sx,
                LOSE_CONFIG.conf[2].ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start?: number) {
        const { LOSE_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : this.animLength,
            data        : [
                MODES.HIDE,
                LOSE_CONFIG.conf[0].ex,
                LOSE_CONFIG.conf[0].sx,
                LOSE_CONFIG.conf[1].ex,
                LOSE_CONFIG.conf[1].sx,
                LOSE_CONFIG.conf[2].ex,
                LOSE_CONFIG.conf[2].sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    showLoseAt () {
        const { MESSAGES, SCENES } = this._config.options;

        const { image, config } = this.info[this.number] || {};
        const shouldUpdate      = !!image && image.attr('src') !== config.srcF;

        shouldUpdate && image.attr('src', config.srcF).scale(1e-5, 1e-5);
        shouldUpdate && this.scaleImage(image);
        shouldUpdate && vibration.vibrate();

        this.number++;

        if (this.number === 3) {
            vibration.gameOver();
            this.number = 0;
            timeline.setTimeout(() => {
                message.postMessage(MESSAGES.GAME_OVER);
                message.postMessage(MESSAGES.SCENE_SWITCH_SCENE, SCENES.GAME_OVER);
            }, 1000);
        }
    }

    scaleImage (image: any) {
        const dur = 500;

        image.myOnScaling = image.myOnScaling || ((time: number, z: number) => {
            image.scale(z = this.back(time, 1e-5, 1 - 1e-5, dur), z);
        });

        image.myOnScaleEnd = image.myOnScaleEnd || function () {
            image.scale(1, 1);
        };

        timeline.createTask({
            start       : 0,
            duration    : dur,
            object      : image,
            onTimeUpdate: image.myOnScaling,
            onTimeEnd   : image.myOnScaleEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (
        time: number,
        mode: Mode,
        x1s: number,
        x1e: number,
        x2s: number,
        x2e: number,
        x3s: number,
        x3e: number,
    ) {
        this.o1.attr('x', this.anim(time, x1s, x1e - x1s, this.animLength));
        this.o2.attr('x', this.anim(time, x2s, x2e - x2s, this.animLength));
        this.o3.attr('x', this.anim(time, x3s, x3e - x3s, this.animLength));
    }

    onTimeStart (mode: Mode) {
        const { MODES } = this._config.options;

        if (mode === MODES.SHOW) {
            [ this.o1, this.o2, this.o3 ].invoke(mode);
            this.resurrect();
        }
    }

    onTimeEnd (mode: Mode) {
        const { MODES } = this._config.options;

        if (mode === MODES.HIDE) {
            [ this.o1, this.o2, this.o3 ].invoke(mode);
            this.die();
        }
    }

    resurrect () {
        this.info.forEach(({ image, config }) => {
            image.attr('src', config.src);
        });
    }

    die () {
        this.info.forEach(({ image, config }) => {
            image.attr('src', config.srcF);
        });
    }
}
