import { MAX_VIEWPORT_HEIGHT, MAX_VIEWPORT_WIDTH } from '../config/constants';


export const viewportSize = {
    viewportHeight: window.innerHeight,
    viewportWidth: window.innerWidth,
};


export const adjustView = (container: HTMLDivElement) => {
    const viewportHeight  = window.innerHeight;
    const viewportWidth   = window.innerWidth;
    const isMaxWidth      = MAX_VIEWPORT_WIDTH < viewportWidth;
    const isMaxHeight     = MAX_VIEWPORT_HEIGHT < viewportHeight;
    const containerWidth  = isMaxWidth ? (MAX_VIEWPORT_WIDTH + 'px') : '100%';
    const containerHeight = isMaxHeight ? (MAX_VIEWPORT_HEIGHT + 'px') : '100%';
    const isAbsolute      = (isMaxWidth || isMaxHeight);
    const position        = isAbsolute ? 'absolute' : 'static';
    const transform       = isAbsolute ? 'translate(-50%, -50%)' : 'none';

    container.style.position  = position;
    container.style.width     = containerWidth;
    container.style.height    = containerHeight;
    container.style.transform = transform;

    const { width, height } = container.getBoundingClientRect();

    viewportSize.viewportHeight = height;
    viewportSize.viewportWidth  = width;
};
