import tween from '../libs/tween';
import message from '../scripts/message';
import timeline from '../scripts/timeline';
import vibration from '../object/vibration';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class BrandIntroScene {
    _group1: any[];

    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    text: any;
    answerText: any;
    answerButton: any;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {
        this._group1 = [];
    }

    set () {
        const { LAYERS, BRAND_INTRO_CONFIG, BRAND_INTRO_BUTTON_CONFIG, SCENES, MESSAGES } = this._config.options;

        BRAND_INTRO_CONFIG.textValue && (this.text = this._layerManager.createText(
            LAYERS.BRAND_INTRO,
            BRAND_INTRO_CONFIG.textValue,
            BRAND_INTRO_CONFIG.textX,
            BRAND_INTRO_CONFIG.textStartY,
            BRAND_INTRO_CONFIG.textFill,
            BRAND_INTRO_CONFIG.textSize,
            BRAND_INTRO_CONFIG.textWeight,
            BRAND_INTRO_CONFIG.textAnchor,
        ).hide());

        BRAND_INTRO_BUTTON_CONFIG.image && (this.answerButton = this._layerManager.createImage(
            LAYERS.BRAND_INTRO,
            BRAND_INTRO_BUTTON_CONFIG.image,
            BRAND_INTRO_BUTTON_CONFIG.buttonX,
            BRAND_INTRO_BUTTON_CONFIG.buttonStartY,
            BRAND_INTRO_BUTTON_CONFIG.width,
            BRAND_INTRO_BUTTON_CONFIG.height,
        ).hide());

        BRAND_INTRO_BUTTON_CONFIG.textValue && (this.answerText = this._layerManager.createText(
            LAYERS.BRAND_INTRO,
            BRAND_INTRO_BUTTON_CONFIG.textValue,
            BRAND_INTRO_BUTTON_CONFIG.textX,
            BRAND_INTRO_BUTTON_CONFIG.textStartY,
            BRAND_INTRO_BUTTON_CONFIG.textFill,
            BRAND_INTRO_BUTTON_CONFIG.textSize,
            BRAND_INTRO_BUTTON_CONFIG.textWeight,
            BRAND_INTRO_BUTTON_CONFIG.textAnchor,
        ).hide());

        this._group1 = this._layerManager.getLayer(LAYERS.BRAND_INTRO).set();
        this.answerText && this._group1.push(this.answerText);
        this.answerButton && this._group1.push(this.answerButton);

        // @ts-ignore
        this._group1.click(() => {
            vibration.vibrate();
            message.postMessage(MESSAGES.SCENE_SWITCH_SCENE, SCENES.BRAND_QUESTION);
        });

        this._layerManager.hideLayer(LAYERS.BRAND_INTRO);
    }

    show (start: number) {
        const { BRAND_INTRO_CONFIG, BRAND_INTRO_BUTTON_CONFIG, MODES } = this._config.options;
        timeline.createTask({
            start       : start,
            duration    : BRAND_INTRO_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                BRAND_INTRO_CONFIG.textStartY,
                BRAND_INTRO_CONFIG.textEndY,
                BRAND_INTRO_BUTTON_CONFIG.textStartY,
                BRAND_INTRO_BUTTON_CONFIG.textEndY,
                BRAND_INTRO_BUTTON_CONFIG.buttonStartY,
                BRAND_INTRO_BUTTON_CONFIG.buttonEndY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start: number) {
        const { BRAND_INTRO_CONFIG, BRAND_INTRO_BUTTON_CONFIG, MODES } = this._config.options;
        timeline.createTask({
            start       : start,
            duration    : BRAND_INTRO_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                BRAND_INTRO_CONFIG.textEndY,
                BRAND_INTRO_CONFIG.textStartY,
                BRAND_INTRO_BUTTON_CONFIG.textEndY,
                BRAND_INTRO_BUTTON_CONFIG.textStartY,
                BRAND_INTRO_BUTTON_CONFIG.buttonEndY,
                BRAND_INTRO_BUTTON_CONFIG.buttonStartY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (
        time: number,
        mode: Mode,
        tsy: number,
        tey: number,
        at1sy: number,
        at1ey: number,
        ab1sy: number,
        ab1ey: number
    ) {
        const { BRAND_INTRO_CONFIG, BRAND_ANSWER1_CONFIG } = this._config.options;
        this.text && this.text.attr('y', this.anim(time, tsy, tey - tsy, BRAND_INTRO_CONFIG.animLength));
        this.answerText && this.answerText.attr('y', this.anim(time, at1sy, at1ey - at1sy, BRAND_ANSWER1_CONFIG.animLength));
        this.answerButton && this.answerButton.attr('y', this.anim(time, ab1sy, ab1ey - ab1sy, BRAND_ANSWER1_CONFIG.animLength));
    }

    onTimeStart (mode: Mode) {
        const { MODES } = this._config.options;

        const group = [];

        if (this.text) group.push(this.text);
        if (this.answerText) group.push(this.answerText);
        if (this.answerButton) group.push(this.answerButton);

        if (mode === MODES.SHOW) {
            group.invoke(mode);
        }
    }

    onTimeEnd (mode: Mode) {
        const { MODES } = this._config.options;

        const group = [];

        if (this.text) group.push(this.text);
        if (this.answerText) group.push(this.answerText);
        if (this.answerButton) group.push(this.answerButton);

        if (mode === MODES.HIDE) {
            group.invoke(mode);
        }
    }
}
