import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';
import { Displacement, DisplacementConfig } from '../object/displacement';


export class DisplacementFactory {
    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    create (config: DisplacementConfig) {
        return new Displacement(this._config, this._layerManager, config);
    }
}
