import './libs/ponyfills';
import './libs/raphael.min';
import { LoggerInterface } from '@kakawajazz/libs';
import { adjustView } from './layout/viewport';
import { Config, ConfigInterface } from './config';
import { Main } from './scripts/main';


export function init (
    options: Partial<ConfigInterface> & { rootElement: HTMLDivElement },
    logger: LoggerInterface = console,
) {
    adjustView(options.rootElement);

    const config = new Config(options, logger);

    new Main(config).start();
}
