import tween from '../libs/tween';
import state from '../scripts/state';
import timeline from '../scripts/timeline';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class GameOverScene {
    image: any;
    anims = [];

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    set () {
        const { LAYERS, GAME_OVER_CONFIG } = this._config.options;

        this.image = this._layerManager.createImage(
            LAYERS.DEFAULT,
            GAME_OVER_CONFIG.image,
            GAME_OVER_CONFIG.x,
            GAME_OVER_CONFIG.y,
            GAME_OVER_CONFIG.width,
            GAME_OVER_CONFIG.height,
        ).hide().scale(
            1e-5,
            1e-5,
        );
    }

    show (start: number) {
        const { MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1e-5, 1, MODES.SHOW ],
            object      : this,
            onTimeUpdate: this.onZooming,
            onTimeStart : this.onZoomStart,
            onTimeEnd   : this.onZoomEnd,
            recycle     : this.anims,
        });
    }

    hide (start: number) {
        const { MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1, 1e-5, MODES.HIDE ],
            object      : this,
            onTimeUpdate: this.onZooming,
            onTimeStart : this.onZoomStart,
            onTimeEnd   : this.onZoomEnd,
            recycle     : this.anims,
        });
    }

    onZoomStart (sz: number, ez: number, mode: Mode) {
        const { MODES } = this._config.options;

        if (mode === MODES.SHOW) this.image.show();
    }

    onZooming (time: number, sz: number, ez: number, z: number) {
        this.image.scale(
            z = tween.exponential.co(
                time,
                sz,
                ez - sz,
                500,
            ),
            z,
        );
    }

    onZoomEnd (sz: number, ez: number, mode: Mode) {
        const { MODES, STATE_KEYS } = this._config.options;

        if (mode === MODES.SHOW) {
            state(STATE_KEYS.CLICK_ENABLE).on();
        } else if (mode === MODES.HIDE) {
            this.image.hide();
        }
    }
}
