import { SoundNames } from '../config/types';
import { FruitFactory } from '../factories/fruit.factory';
import { LayerManager } from '../managers/layer.manager';
import { Fruit } from '../object/fruit';
import { Sound } from '../object/sound';
import { SoundFactory } from '../factories/sound.factory';
import { Light } from '../object/light';

import timeline from './timeline';
import state from './state';
import message from './message';

import { Score } from '../object/score';
import { Background } from '../object/background';
import { Knifes } from '../object/knifes';
import { Lose } from '../object/lose';
import vibration from '../object/vibration';
import { randomNumber } from './utils';
import { viewportSize } from '../layout/viewport';
import { Config } from '../config';


export class Game {
    _light: Light;

    scoreNumber                   = 0;
    volleyNum                     = 2;
    volleyMultipleNumber          = 5;
    fruits: Fruit[]               = [];
    gameInterval: Interval | null = null;
    snd: Sound;
    boomSnd: Sound;

    constructor (
        private readonly _config: Config,
        private readonly _fruitFactory: FruitFactory,
        private readonly _layerManager: LayerManager,
        private readonly _knifes: Knifes,
        private readonly _soundFactory: SoundFactory,
        private readonly _score: Score,
        private readonly _lose: Lose,
        private readonly _background: Background,
    ) {
        this._light = new Light(this._config, this._layerManager);

        const { FRUIT_CONFIG, MESSAGES, SOUNDS } = this._config.options;

        this.snd     = this._soundFactory.create(SOUNDS[SoundNames.THROW]);
        this.boomSnd = this._soundFactory.create(SOUNDS[SoundNames.BOOM]);

        message.addEventListener(MESSAGES.FRUIT_REMOVE, (fruit: Fruit) => {
            const index = this.fruits.indexOf(fruit);

            if (index > -1) {
                this.fruits.splice(index, 1);
            }
        });

        message.addEventListener(MESSAGES.FRUIT_FALL_OUT_OF_VIEWER, (fruit: Fruit) => {
            if (fruit.type === FRUIT_CONFIG.types.BOOM) return;
            this._lose.showLoseAt();
        });

        message.addEventListener(MESSAGES.GAME_OVER, () => {
            this.gameOver();
            this._knifes.switchOn();
        });

        message.addEventListener(MESSAGES.OVER_WHITE_LIGHT_SHOW, () => {
            this._knifes.endAll();

            for (let i = this.fruits.length - 1; i >= 0; i--) {
                this.fruits[i].remove();
            }

            this._background.stop();
        });
    }

    barbette = () => {
        if (this.fruits.length >= this.volleyNum) {
            return;
        }

        const { viewportHeight, viewportWidth } = viewportSize;
        const startX                            = randomNumber(viewportWidth);
        const endX                              = randomNumber(viewportWidth);
        const startY                            = viewportHeight;
        const fruit                             = this._fruitFactory.create(startX, startY).shotOut(0, endX);

        this.fruits.push(fruit);
        this.snd.play();

        this.barbette();
    };

    start () {
        const { STATE_KEYS, GAME_STATES, FIRST_SAFE_FRUIT_NUMBER } = this._config.options;

        this._lose.number             = 0;
        this._fruitFactory.safeNumber = FIRST_SAFE_FRUIT_NUMBER;

        this.scoreNumber = 0;
        this.volleyNum   = 2;

        timeline.setTimeout(() => {
            state(STATE_KEYS.GAME_STATE).set(GAME_STATES.PLAYING);
            this.gameInterval = timeline._setInterval(this.barbette, 1e3);
        }, 500);
    }

    gameOver () {
        const { STATE_KEYS, GAME_STATES } = this._config.options;

        state(STATE_KEYS.GAME_STATE).set(GAME_STATES.OVER);
        this.gameInterval && this.gameInterval.stop();
        this.fruits.length = 0;
    }

    applyScore (score: number) {
        if (score > this.volleyNum * this.volleyMultipleNumber) {
            this.volleyNum++;
        }
        this.volleyMultipleNumber += 50;
    }

    sliceAt (fruit: Fruit, angle: number) {
        const { FRUIT_CONFIG, STATE_KEYS, GAME_STATES } = this._config.options;

        if (state(STATE_KEYS.GAME_STATE).isnot(GAME_STATES.PLAYING)) return;

        if (fruit.type === FRUIT_CONFIG.types.BOOM) {
            this.onHitBoom(fruit);
        } else {
            this.onHitFruit(fruit, angle);
        }
    }

    catchFruit (fruit: Fruit) {
        const { FRUIT_CONFIG, STATE_KEYS, GAME_STATES } = this._config.options;

        if (state(STATE_KEYS.GAME_STATE).isnot(GAME_STATES.PLAYING)) return;

        if (fruit.type === FRUIT_CONFIG.types.BOOM) {
            this.onHitBoom(fruit);
        } else {
            this.onHitFruit(fruit);
        }
    }

    onHitFruit (fruit: Fruit, angle = 0) {
        const index                = this.fruits.indexOf(fruit);
        const { SCORE_MULTIPLIER } = this._config.options;

        fruit.broken(angle);
        vibration.fruit();
        if (index) this.fruits.splice(index, 1);
        this.scoreNumber += (SCORE_MULTIPLIER || 0);
        this._score.number(this.scoreNumber);
        this.applyScore(this.scoreNumber);
    }

    onHitBoom (fruit: Fruit) {
        this._knifes.switchOn();
        this._lose.die();
        this.pauseAllFruit();
        this.boomSnd.play();
        vibration.boom();
        this._light.start(fruit, this._background.stop);
    }

    pauseAllFruit () {
        this.gameInterval && this.gameInterval.stop();
        this._knifes.pause();
        this.fruits.invoke('pause');
    }
}
