import tween from '../libs/tween';
import timeline from '../scripts/timeline';
import message from '../scripts/message';
import vibration from './vibration';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class SoundButton {
    startText: any;
    gameText: any;
    resultText: any;
    startSoundButton: any;
    gameSoundButton: any;
    resultSoundButton: any;
    startGroup: any;
    gameGroup: any;
    resultGroup: any;
    anims = [];
    anim  = tween.exponential.co;
    isOn = false;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager
    ) {}

    set () {
        const { LAYERS, SOUND_BUTTON_CONFIG, MESSAGES } = this._config.options;

        this.startSoundButton = this._layerManager.createImage(
            LAYERS.START_SCREEN,
            this.isOn ? SOUND_BUTTON_CONFIG.imageOn : SOUND_BUTTON_CONFIG.imageOff,
            SOUND_BUTTON_CONFIG.imageX,
            SOUND_BUTTON_CONFIG.imageStartY,
            SOUND_BUTTON_CONFIG.imageWidth,
            SOUND_BUTTON_CONFIG.imageHeight,
        ).hide();

        this.startText = this._layerManager.createText(
            LAYERS.START_SCREEN,
            this.isOn ? SOUND_BUTTON_CONFIG.textValueOff : SOUND_BUTTON_CONFIG.textValueOn,
            SOUND_BUTTON_CONFIG.textX,
            SOUND_BUTTON_CONFIG.textStartY,
            SOUND_BUTTON_CONFIG.textFill,
            SOUND_BUTTON_CONFIG.textSize,
            SOUND_BUTTON_CONFIG.textWeight,
            SOUND_BUTTON_CONFIG.textAnchor,
        );

        this.startGroup = this._layerManager.getLayer(LAYERS.START_SCREEN).set();
        this.startGroup.push(this.startSoundButton, this.startText);

        this.startGroup.click(() => {
            this.toggle();
            message.postMessage(MESSAGES.TOGGLE_SOUND, null);
            vibration.soundButton();
        });

        this.gameSoundButton = this._layerManager.createImage(
            LAYERS.FRUIT,
            this.isOn ? SOUND_BUTTON_CONFIG.imageOn : SOUND_BUTTON_CONFIG.imageOff,
            SOUND_BUTTON_CONFIG.imageX,
            SOUND_BUTTON_CONFIG.imageStartY,
            SOUND_BUTTON_CONFIG.imageWidth,
            SOUND_BUTTON_CONFIG.imageHeight,
        ).hide();

        this.gameText = this._layerManager.createText(
            LAYERS.FRUIT,
            this.isOn ? SOUND_BUTTON_CONFIG.textValueOff : SOUND_BUTTON_CONFIG.textValueOn,
            SOUND_BUTTON_CONFIG.textX,
            SOUND_BUTTON_CONFIG.textStartY,
            SOUND_BUTTON_CONFIG.textFill,
            SOUND_BUTTON_CONFIG.textSize,
            SOUND_BUTTON_CONFIG.textWeight,
            SOUND_BUTTON_CONFIG.textAnchor,
        );

        this.gameGroup = this._layerManager.getLayer(LAYERS.FRUIT).set();
        this.gameGroup.push(this.gameSoundButton, this.gameText);

        this.gameGroup.click(() => {
            this.toggle();
            message.postMessage(MESSAGES.TOGGLE_SOUND, null);
            vibration.soundButton();
        });

        this.resultSoundButton = this._layerManager.createImage(
            LAYERS.RESULT_SCREEN,
            this.isOn ? SOUND_BUTTON_CONFIG.imageOn : SOUND_BUTTON_CONFIG.imageOff,
            SOUND_BUTTON_CONFIG.imageX,
            SOUND_BUTTON_CONFIG.imageStartY,
            SOUND_BUTTON_CONFIG.imageWidth,
            SOUND_BUTTON_CONFIG.imageHeight,
        ).hide();

        this.resultText = this._layerManager.createText(
            LAYERS.RESULT_SCREEN,
            this.isOn ? SOUND_BUTTON_CONFIG.textValueOff : SOUND_BUTTON_CONFIG.textValueOn,
            SOUND_BUTTON_CONFIG.textX,
            SOUND_BUTTON_CONFIG.textStartY,
            SOUND_BUTTON_CONFIG.textFill,
            SOUND_BUTTON_CONFIG.textSize,
            SOUND_BUTTON_CONFIG.textWeight,
            SOUND_BUTTON_CONFIG.textAnchor,
        );

        this.resultGroup = this._layerManager.getLayer(LAYERS.START_SCREEN).set();
        this.resultGroup.push(this.resultSoundButton, this.resultText);

        this.resultGroup.click(() => {
            this.toggle();
            message.postMessage(MESSAGES.TOGGLE_SOUND, null);
            vibration.soundButton();
        });
    }

    show (start: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.imageHomeEndY,
                SOUND_BUTTON_CONFIG.textStartY,
                SOUND_BUTTON_CONFIG.textHomeEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hide (start: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                SOUND_BUTTON_CONFIG.imageHomeEndY,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.textHomeEndY,
                SOUND_BUTTON_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    showHomeSoundButton (start: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.imageHomeEndY,
                SOUND_BUTTON_CONFIG.textStartY,
                SOUND_BUTTON_CONFIG.textHomeEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    showResultSoundButton (start?: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.imageResultEndY,
                SOUND_BUTTON_CONFIG.textStartY,
                SOUND_BUTTON_CONFIG.textResultEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    showGameSoundButton (start?: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.imageGameEndY,
                SOUND_BUTTON_CONFIG.textStartY,
                SOUND_BUTTON_CONFIG.textGameEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hideHomeSoundButton (start?: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.HIDE,
                SOUND_BUTTON_CONFIG.imageHomeEndY,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.textHomeEndY,
                SOUND_BUTTON_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hideResultSoundButton (start?: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.HIDE,
                SOUND_BUTTON_CONFIG.imageResultEndY,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.textResultEndY,
                SOUND_BUTTON_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hideGameSoundButton (start?: number) {
        const { SOUND_BUTTON_CONFIG, MODES } = this._config.options;

        timeline.createTask({
            start       : start,
            duration: SOUND_BUTTON_CONFIG.animLength,
            data    : [
                MODES.HIDE,
                SOUND_BUTTON_CONFIG.imageGameEndY,
                SOUND_BUTTON_CONFIG.imageStartY,
                SOUND_BUTTON_CONFIG.textGameEndY,
                SOUND_BUTTON_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    onTimeUpdate (time: number, mode: Mode, bsy: number, bey: number, tsy: number, tey: number) {
        const { HOME_TEXT_CONFIG } = this._config.options;

        this.startSoundButton.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.startText.attr('y', this.anim(time, tsy, tey - tsy, HOME_TEXT_CONFIG.animLength));
        this.gameSoundButton.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.gameText.attr('y', this.anim(time, tsy, tey - tsy, HOME_TEXT_CONFIG.animLength));
        this.resultSoundButton.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.resultText.attr('y', this.anim(time, tsy, tey - tsy, HOME_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode: Mode) {
        const { MODES } = this._config.options;

        if (mode === MODES.SHOW) [
            this.startSoundButton,
            this.startText,
            this.gameSoundButton,
            this.gameText,
            this.resultSoundButton,
            this.resultText
        ].invoke(mode);
    }

    onTimeEnd (mode: Mode) {
        const { MODES } = this._config.options;

        if (mode === MODES.HIDE) [
            this.startSoundButton,
            this.startText,
            this.gameSoundButton,
            this.gameText,
            this.resultSoundButton,
            this.resultText
        ].invoke(mode);
    }

    toggle () {
        this.isOn ? this.off() : this.on();
    }

    on () {
        const { SOUND_BUTTON_CONFIG } = this._config.options;

        this.startText.attr('text', SOUND_BUTTON_CONFIG.textValueOff);
        this.startSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOn);
        this.gameText.attr('text', SOUND_BUTTON_CONFIG.textValueOff);
        this.gameSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOn);
        this.resultText.attr('text', SOUND_BUTTON_CONFIG.textValueOff);
        this.resultSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOn);
        this.isOn = true;
    }

    off () {
        const { SOUND_BUTTON_CONFIG } = this._config.options;

        this.startText.attr('text', SOUND_BUTTON_CONFIG.textValueOn);
        this.startSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOff);
        this.gameText.attr('text', SOUND_BUTTON_CONFIG.textValueOn);
        this.gameSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOff);
        this.resultText.attr('text', SOUND_BUTTON_CONFIG.textValueOn);
        this.resultSoundButton.attr('src', SOUND_BUTTON_CONFIG.imageOff);
        this.isOn = false;
    }
}
