import message from '../scripts/message';
import timeline from '../scripts/timeline';
import { randomNumber } from '../scripts/utils';
import { viewportSize } from '../layout/viewport';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';
import { Fruit } from './fruit';


export class Light {
    lights: any[]     = [];
    indexes: number[] = [];

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {
        const { LAYERS, LIGHTS_CONFIG } = this._config.options;

        for (let i = 0; i < LIGHTS_CONFIG.number; i++) {
            this.indexes[i] = i;
        }

        this._layerManager.hideLayer(LAYERS.LIGHT);
        this._layerManager.hideLayer(LAYERS.MASK);
    }

    start (boom: Fruit, callback: () => void) {
        const { LIGHTS_CONFIG } = this._config.options;

        const x   = boom.originX;
        const y   = boom.originY;
        const idx = this.indexes.random();
        let time  = 0;

        const b = (i: number) => {
            this.build(x, y, idx[i]);
        };

        for (let i = 0; i < LIGHTS_CONFIG.number; i++) {
            timeline.setTimeout(() => b(i), time += LIGHTS_CONFIG.appearanceTime);
        }

        timeline.setTimeout(() => {
            this.overWhiteLight();
            callback && callback();
        }, time);
    }

    overWhiteLight = () => {
        const { LAYERS, LIGHTS_CONFIG, MESSAGES, SCENES } = this._config.options;

        const maskLayer                         = this._layerManager.getLayer(LAYERS.MASK);
        const { viewportHeight, viewportWidth } = viewportSize;
        message.postMessage(MESSAGES.OVER_WHITE_LIGHT_SHOW);
        this.removeLights();

        const dur     = LIGHTS_CONFIG.maskAppearanceTime;
        const mask    = maskLayer.rect(0, 0, viewportWidth, viewportHeight).attr({
            fill  : LIGHTS_CONFIG.fill,
            stroke: 'none',
        });
        const control = {
            onTimeUpdate: function (time: number) {
                mask.attr('opacity', 1 - time / dur);
            },

            onTimeEnd: () => {
                mask.remove();
                message.postMessage(MESSAGES.GAME_OVER);
                message.postMessage(MESSAGES.SCENE_SWITCH_SCENE, SCENES.GAME_OVER);
                this._layerManager.hideLayer(LAYERS.LIGHT);
                this._layerManager.hideLayer(LAYERS.MASK);
            },
        };

        this._layerManager.showLayer(LAYERS.MASK);

        timeline.createTask({
            start       : 0,
            duration    : dur,
            object      : control,
            onTimeUpdate: control.onTimeUpdate,
            onTimeEnd   : control.onTimeEnd,
        });
    };

    removeLights () {
        const lightsLength = this.lights.length;
        for (let i = 0; i < lightsLength; i++) {
            this.lights[i].remove();
        }
        this.lights.length = 0;
    }

    build (x: number, y: number, r: number) {
        const { LAYERS, LIGHTS_CONFIG } = this._config.options;

        const lightLayer        = this._layerManager.getLayer(LAYERS.LIGHT);
        const { viewportWidth } = viewportSize;
        let a1                  = r * 36 + randomNumber(10);
        let a2                  = a1 + 5;

        a1 = Math.PI * a1 / 180;
        a2 = Math.PI * a2 / 180;

        const x1    = x + (2 * viewportWidth) * Math.cos(a1);
        const y1    = y + (2 * viewportWidth) * Math.sin(a1);
        const x2    = x + (2 * viewportWidth) * Math.cos(a2);
        const y2    = y + (2 * viewportWidth) * Math.sin(a2);
        const light = lightLayer.path([ 'M', x, y, 'L', x1, y1, 'L', x2, y2, 'Z' ]).attr({
            stroke: 'none',
            fill  : LIGHTS_CONFIG.fill,
        });

        this._layerManager.showLayer(LAYERS.LIGHT);

        this.lights.push(light);
    }
}
