const VIBRATION_PATTERNS = {
    DEFAULT     : [ 100 ],
    SOUND_BUTTON: [ 30 ],
    FRUIT       : [ 30 ],
    GAME_OVER   : [ 10, 0, 10, 0, 100, 0, 600 ],
    BOOM        : [ 10, 0, 10, 0, 100, 0, 600, 0, 10, 0, 10, 0, 100, 0, 400 ],
};


class Vibration {
    vibrate (pattern = VIBRATION_PATTERNS.DEFAULT) {
        navigator.vibrate && navigator.vibrate(pattern);
    }

    soundButton () {
        this.vibrate(VIBRATION_PATTERNS.SOUND_BUTTON);
    }

    fruit () {
        this.vibrate(VIBRATION_PATTERNS.FRUIT);
    }

    gameOver () {
        this.vibrate(VIBRATION_PATTERNS.GAME_OVER);
    }

    boom () {
        this.vibrate(VIBRATION_PATTERNS.BOOM);
    }
}


export default new Vibration();
