import { randomNumber } from '../scripts/utils';


export function addPonyfills () {
    String.prototype.trim = String.prototype.trim || function () {
        return this.replace(/^\s+|\s+$/, "");
    };

    String.prototype.htmlEncode = String.prototype.htmlEncode || function () {
        const div = document.createElement('div');
        return function () {
            let text;
            div.appendChild(document.createTextNode(this));
            text          = div.innerHTML;
            div.innerHTML = "";
            return text;
        };
    }();

    String.prototype.byteLength = String.prototype.byteLength || function () {
        return this.replace(/[^\x00-\xff]/g, "  ").length;
    };

    String.prototype.subByte = String.prototype.subByte || function (len, tail) {
        let s = this;
        if (s.byteLength() <= len) return s;
        tail = tail || "";
        len -= tail.byteLength();
        return s = s.slice(0, len).replace(/([^\x00-\xff])/g, "$1 ")
            .slice(0, len)
            .replace(/[^\x00-\xff]$/, "")
            .replace(/([^\x00-\xff]) /g, "$1") + tail;
    }

    Function.prototype.defer = Function.prototype.defer || function (scope, timeout) {
        const me = this;
        const fn = function () {
            me.apply(scope, arguments);
        };
        return setTimeout(fn, timeout);
    };

    Function.prototype.bind = Function.prototype.bind || function (scope) {
        const me = this;
        return function () {
            return me.apply(scope, arguments);
        }
    };

    Function.prototype.saturate = Function.prototype.saturate || function (scope/*, args */) {
        const fn = this, afters = [].slice.call(arguments, 1);
        return function () {
            return fn.apply(scope, [].slice.call(arguments, 0).concat(afters));
        }
    };

    Array.prototype.indexOf = Array.prototype.indexOf || function (item, i) {
        const length = this.length;

        if (!i) i = 0;

        if (i < 0) i = length + i;

        for (; i < length; i++) {
            if (this[i] === item) return i;
        }

        return -1;
    };

    Array.prototype.every = Array.prototype.every || function (fn, context) {
        for (var i = 0, len = this.length; i < len; i++) {
            if (!fn.call(context, this[i], i, this)) return false;
        }
        return true;
    };

    Array.prototype.filter = Array.prototype.filter || function (fn, context) {
        let result = [], val;
        for (var i = 0, len = this.length; i < len; i++) {
            if (val = this[i], fn.call(context, val, i, this)) result.push(val);
        }
        return result;
    };

    Array.prototype.forEach = Array.prototype.forEach || function (fn, context) {
        for (var i = 0, len = this.length; i < len; i++)
            fn.call(context, this[i], i, this);
    };

    Array.prototype.map = Array.prototype.map || function (fn, context) {
        const result = [];
        for (var i = 0, len = this.length; i < len; i++)
            result[i] = fn.call(context, this[i], i, this);
        return result;
    };

    Array.prototype.some = Array.prototype.some || function (fn, context) {
        for (var i = 0, len = this.length; i < len; i++)
            if (fn.call(context, this[i], i, this))
                return true;
        return false;
    };

    Array.prototype.invoke = Array.prototype.invoke || function (method /*, args */) {
        const args = [].slice.call(arguments, 1);
        this.forEach(function (item) {
            if (item instanceof Array)
                item[0][method].apply(item[0], item.slice(1));
            else
                item[method].apply(item, args);
        });
        return this;
    };

    Array.prototype.random = Array.prototype.random || function () {
        let arr = this.slice(0), ret = [], i = arr.length;
        while (i--)
            ret.push(arr.splice(randomNumber(i + 1), 1)[0]);
        return ret;
    };
}

addPonyfills();
