import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';
import { KnifePart } from './knife-part';


export class Knifes {
    _knifeParts: KnifePart[] = [];
    lastX: number | null     = null;
    lastY: number | null     = null;
    switchState              = true;

    _anims: any = [];

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    get anims () {
        return this._anims;
    }

    newKnife () {
        this.lastX = null;
        this.lastY = null;
    }

    through (x: number, y: number) {
        if (!this.switchState) return;

        let ret = null;

        if (this.lastX !== null && (this.lastX !== x || this.lastY !== y)) {
            this.push(new KnifePart(
                this._config,
                this._layerManager,
                this,
                {
                    sx: this.lastX,
                    sy: this.lastY,
                    ex: x,
                    ey: y,
                }
            ).set());
            ret = [ this.lastX, this.lastY, x, y ];
        }

        this.lastX = x;
        this.lastY = y;

        return ret;
    }

    pause () {
        this.anims.clear && this.anims.clear();
        this.switchOff();
    }

    switchOff () {
        this.switchState = false;
    }

    switchOn () {
        this.switchState = true;
        this.endAll();
    }

    endAll () {
        for (let i = this._knifeParts.length - 1; i >= 0; i--) {
            this._knifeParts[i].end();
        }
    }

    push (knifePart: KnifePart) {
        this._knifeParts.push(knifePart);
    }

    indexOf (knifePart: KnifePart) {
        return this._knifeParts.indexOf(knifePart);
    }

    splice (index: number, count: number) {
        this._knifeParts.splice(index, count);
    }
}
