import { isHTMLElement, LoggerInterface, noop } from '@kakawajazz/libs';
import { ConfigInterface } from './config.interface';
import { APINinjaConfig, NinjaConfig } from './namespace';
import { GameStates, InteractionModes, Layers, Messages, Modes, Scenes, SceneStates, StateKeys } from './types';
import { FIRST_SAFE_FRUIT_NUMBER, FONT_FAMILY, INTERACTION_MODE, SCORE_MULTIPLIER } from './constants';
import * as C from './converter';


export class Config implements ConfigInterface {
    private readonly _rootElement: HTMLDivElement;
    private readonly _onReady: () => void;
    private readonly _onReset: () => void;
    private readonly _onSuccess: () => void;
    private readonly _onError: (error: string) => void;
    private _options: NinjaConfig.Model;

    constructor (
        private readonly _params: Partial<ConfigInterface> & APINinjaConfig.Model & { rootElement: HTMLDivElement },
        private readonly _logger: LoggerInterface,
    ) {
        this._onReady     = this._params.onReady ?? noop;
        this._onReset     = this._params.onReset ?? noop;
        this._onSuccess   = this._params.onSuccess ?? noop;
        this._onError     = this._params.onError ?? noop;
        this._rootElement = this.getRoot(this._params.rootElement);

        this._logger.debug('Setting config start');
        this._options = this.convertOptions(this._params);
        this._logger.debug('Setting config end');
    }

    convertOptions (params: APINinjaConfig.Model) {
        const interactionMode        = params.INTERACTION_MODE || INTERACTION_MODE;
        const homeLogoConfig         = C.convertHomeLogoConfig(params.HOME_LOGO_CONFIG);
        const scoreConfig            = C.convertScoreConfig(params.SCORE_CONFIG);
        const resultLogoConfig       = C.convertResultLogoConfig(params.RESULT_LOGO_CONFIG);
        const resultButtonNextConfig = C.convertResultButtonNextConfig(params.RESULT_BUTTON_NEXT_CONFIG, resultLogoConfig);
        const brandAnswer1ButtonConfig = C.convertBrandAnswer1ButtonConfig(params.BRAND_ANSWER1_CONFIG);

        return {
            BACKGROUND_CONFIG          : C.convertBackgroundConfig(params.BACKGROUND_CONFIG),
            BOTTOM_CONFIG              : C.convertBottomConfig(params.BOTTOM_CONFIG),
            BRAND_ANSWER1_CONFIG       : brandAnswer1ButtonConfig,
            BRAND_ANSWER2_CONFIG       : C.convertBrandAnswer2ButtonConfig(params.BRAND_ANSWER2_CONFIG, brandAnswer1ButtonConfig),
            BRAND_INTRO_BUTTON_CONFIG  : C.convertBrandIntroButtonConfig(params.BRAND_INTRO_BUTTON_CONFIG),
            BRAND_INTRO_CONFIG         : C.convertBrandIntroConfig(params.BRAND_INTRO_CONFIG),
            BRAND_QUESTION_TEXT1_CONFIG: C.convertBrandQuestionText1(params.BRAND_QUESTION_TEXT1_CONFIG),
            BRAND_QUESTION_TEXT2_CONFIG: C.convertBrandQuestionText2(params.BRAND_QUESTION_TEXT2_CONFIG),
            CONSOLE_CONFIG             : C.convertConsoleConfig(),
            FIRST_SAFE_FRUIT_NUMBER    : params.FIRST_SAFE_FRUIT_NUMBER && Number(params.FIRST_SAFE_FRUIT_NUMBER) || FIRST_SAFE_FRUIT_NUMBER,
            FONT_FAMILY                : params.FONT_FAMILY || FONT_FAMILY,
            FLASH_CONFIG               : C.convertFlashConfig(params.FLASH_CONFIG),
            FPS_CONFIG                 : C.convertFPSConfig(),
            FRUIT_CONFIG               : C.convertFruitsConfig(params.FRUIT_CONFIG, interactionMode),
            GAME_OVER_CONFIG           : C.convertGameOverConfig(params.GAME_OVER_CONFIG),
            GAME_STATES                : GameStates,
            HOME_LOGO_CONFIG           : homeLogoConfig,
            HOME_MASK_CONFIG           : C.convertHomeMaskConfig(),
            HOME_TEXT_CONFIG           : C.convertHomeTextConfig(params.HOME_TEXT_CONFIG, homeLogoConfig),
            INTERACTION_MODE           : interactionMode,
            INTERACTION_MODES          : InteractionModes,
            IS_TOUCH                   : interactionMode === InteractionModes.TOUCH,
            IS_DEBUG                   : new URLSearchParams(window.location.search).has('debug'),
            JUICE_CONFIG               : C.convertJuiceConfig(params.JUICE_CONFIG),
            KNIFE_PART_CONFIG          : C.convertKnifePartConfig(),
            LAYERS                     : Layers,
            LIGHTS_CONFIG              : C.convertLightsConfig(),
            LOSE_CONFIG                : C.convertLoseConfig(params.LOSE_CONFIG, scoreConfig),
            MESSAGES                   : Messages,
            MODES                      : Modes,
            RESULT_BUTTON_NEXT_CONFIG  : resultButtonNextConfig,
            RESULT_BUTTON_REPLAY_CONFIG: C.convertResultButtonReplayConfig(params.RESULT_BUTTON_REPLAY_CONFIG, resultButtonNextConfig),
            RESULT_LOGO_CONFIG         : resultLogoConfig,
            RESULT_TEXT_CONFIG         : C.convertResultTextConfig(params.RESULT_TEXT_CONFIG, resultLogoConfig),
            SCENES                     : Scenes,
            SCENE_STATES               : SceneStates,
            SCORE_CONFIG               : scoreConfig,
            SCORE_MULTIPLIER           : params.SCORE_MULTIPLIER && Number(params.SCORE_MULTIPLIER) || SCORE_MULTIPLIER,
            SOUNDS                     : C.convertSounds(params.SOUNDS),
            SOUND_BUTTON_CONFIG        : C.convertSoundButtonConfig(params.START_BUTTON_CONFIG, scoreConfig),
            START_BUTTON_CONFIG        : C.convertStartButtonConfig(params.START_BUTTON_CONFIG, homeLogoConfig),
            STATE_KEYS                 : StateKeys,
        };
    }

    getRoot (rootElement: HTMLDivElement) {
        if (!isHTMLElement(rootElement) || rootElement.tagName !== 'DIV') {
            this._logger.error('Root element is not an HTMLDivElement');
            this.onError('config.rootElement.error');
            return rootElement;
        }

        return rootElement;
    }

    set options (options: NinjaConfig.Model) {
        this._options = options;
    }

    get options () {
        return this._options;
    }

    get onReady () {
        return this._onReady;
    }

    get onReset () {
        return this._onReset;
    }

    get onSuccess () {
        return this._onSuccess;
    }

    get onError () {
        return this._onError;
    }

    get rootElement () {
        return this._rootElement;
    }
}
