import { FruitTypes, InteractionModes, JuiceShapes, TextAnchor, TextWeight } from './types';
import { APINinjaConfig, NinjaConfig } from './namespace';
import { calcResultFontSize, calcSize, calcVerticalSize } from '../layout/calc-size';
import { viewportSize } from '../layout/viewport';
import * as C from './constants';


export const convertBackgroundConfig = (apiConfig: APINinjaConfig.BackgroundConfig = {}): NinjaConfig.BackgroundConfig => {
    return {
        height             : viewportSize.viewportHeight,
        image              : apiConfig.image || C.BACKGROUND_IMAGE,
        preserveAspectRatio: 'xMidYMid slice',
        width              : viewportSize.viewportWidth,
        x                  : 0,
        y                  : 0,
    };
};

export const convertBottomConfig = (apiConfig: APINinjaConfig.BottomConfig = {}): NinjaConfig.BottomConfig => {
    const height = calcSize(apiConfig.height || C.BOTTOM_HEIGHT);

    return {
        duration: C.BOTTOM_DURATION,
        endX    : 0,
        endY    : viewportSize.viewportHeight - height,
        height,
        image   : apiConfig.image || C.BOTTOM_IMAGE,
        startX  : 0,
        startY  : viewportSize.viewportHeight,
        width   : viewportSize.viewportWidth,
    };
};

export const convertBrandAnswer1ButtonConfig = (apiConfig: APINinjaConfig.ButtonConfig = {}): NinjaConfig.ButtonConfig => {
    const buttonEndY = calcVerticalSize(apiConfig.buttonEndY || C.BRAND_ANSWER_BUTTON1_END_Y);
    const height     = calcSize(apiConfig.height || C.BRAND_ANSWER_BUTTON1_HEIGHT);

    return {
        animLength  : C.BRAND_QUESTION_ANIMATION_LENGTH,
        buttonEndY,
        buttonStartY: viewportSize.viewportHeight,
        buttonX     : calcSize(apiConfig.buttonX || C.BRAND_ANSWER_BUTTON1_X),
        height,
        image       : apiConfig.image || C.BRAND_ANSWER_BUTTON1_IMAGE,
        textAnchor  : apiConfig.textAnchor || TextAnchor.MIDDLE,
        textEndY    : buttonEndY - calcVerticalSize(6) + height / 2,
        textFill    : apiConfig.textFill || C.BRAND_QUESTION_FILL,
        textSize    : calcVerticalSize(apiConfig.textSize || C.BRAND_ANSWER_TEXT1_SIZE) + 'px',
        textStartY  : viewportSize.viewportHeight + height,
        textValue   : apiConfig.textValue || C.BRAND_ANSWER_TEXT1_VALUE,
        textWeight  : apiConfig.textWeight || TextWeight.BOLD,
        textX       : viewportSize.viewportWidth / 2,
        width       : calcSize(apiConfig.width || C.BRAND_ANSWER_BUTTON1_WIDTH),
    };
};

