const convertLose = (
    apiConfig: APINinjaConfig.Lose,
    scoreConfig: NinjaConfig.Score,
    index: number,
): NinjaConfig.LoseConf => {
    const width = calcSize(apiConfig.width || C.LOSE_WIDTH);
    const left  = viewportSize.viewportWidth - scoreConfig.imageEx - width;
    const ex    = left - index * width;

    return {
        ex,
        h   : calcSize(apiConfig.height || C.LOSE_HEIGHT),
        src : apiConfig.emptyImage || C.LOSE_IMAGE,
        srcF: apiConfig.fullImage || C.LOSE_F_IMAGE,
        sx  : ex + 200,
        w   : width,
        y   : scoreConfig.imageY,
    };
};

export const convertLoseConfig = (
    apiConfig: APINinjaConfig.Lose = {},
    scoreConfig: NinjaConfig.Score
): NinjaConfig.Lose => {
    const conf: NinjaConfig.LoseConf[] = [];

    for (let i = 0; i < C.LOSE_NUMBER; i++) {
        conf.push(convertLose(apiConfig, scoreConfig, i));
    }

    return {
        animLength: C.LOSE_ANIMATION_LENGTH,
        conf,
    };
};

export const convertScoreConfig = (apiConfig: APINinjaConfig.Score = {}): NinjaConfig.Score => {
    const imageEx = calcSize(apiConfig.imageEx || C.SCORE_IMAGE_EX);
    const imageH  = calcSize(apiConfig.imageH || C.SCORE_IMAGE_H);
    const imageW  = calcSize(apiConfig.imageW || C.SCORE_IMAGE_W);
    const imageY  = calcSize(apiConfig.imageY || C.SCORE_IMAGE_Y);

    return {
        animLength : C.SCORE_ANIM_LENGTH,
        image      : apiConfig.image || C.SCORE_IMAGE,
        imageEx,
        imageH,
        imageSx    : C.SCORE_IMAGE_SX,
        imageW,
        imageY     : calcSize(apiConfig.imageY || C.SCORE_IMAGE_Y),
        text1Ex    : imageEx + imageW + calcSize(12),
        text1Fill  : apiConfig.text1Fill || C.SCORE_TEXT_FILL,
        text1Size  : calcVerticalSize(apiConfig.text1Size || C.SCORE_TEXT1_SIZE) + 'px',
        text1Sx    : C.SCORE_TEXT1_SX,
        text1Value : apiConfig.text1Value || C.SCORE_TEXT1_VALUE,
        text1Weight: apiConfig.text1Weight || TextWeight.BOLD,
        text1Y     : imageY + imageH - calcSize(14),
        text2Ex    : imageEx,
        text2Fill  : apiConfig.text2Fill || C.SCORE_TEXT_FILL,
        text2Prefix: apiConfig.text2Prefix || C.SCORE_TEXT2_PREFIX,
        text2Size  : calcVerticalSize(apiConfig.text2Size || C.SCORE_TEXT2_SIZE) + 'px',
        text2Sx    : C.SCORE_TEXT2_SX,
        text2Value : apiConfig.text2Value || C.SCORE_TEXT2_VALUE,
        text2Weight: apiConfig.text2Weight || TextWeight.BOLD,
        text2Y     : calcSize(apiConfig.text2Y || C.SCORE_TEXT2_Y),
    };
};

