export const convertSounds = (apiConfig: APINinjaConfig.Sounds = {}): NinjaConfig.Sounds => {
    return {
        ...C.SOUNDS,
        ...apiConfig,
    };
};

export const convertSoundButtonConfig = (
    apiConfig: APINinjaConfig.SoundButton = {},
    scoreConfig: NinjaConfig.Score,
): NinjaConfig.SoundButton => {
    const textSize   = calcVerticalSize(apiConfig.textSize || C.SOUND_TEXT_SIZE);
    const imageWidth = calcSize(apiConfig.imageWidth || C.SOUND_BUTTON_IMAGE_WIDTH);
    const imageX     = viewportSize.viewportWidth - imageWidth - calcSize(38);

    return {
        animLength     : C.SOUND_BUTTON_ANIMATION_LENGTH,
        imageGameEndY  : scoreConfig.imageY + scoreConfig.imageH + calcSize(4),
        imageHeight    : calcSize(apiConfig.imageHeight || C.SOUND_BUTTON_IMAGE_HEIGHT),
        imageHomeEndY  : calcVerticalSize(apiConfig.imageHomeEndY || C.SOUND_BUTTON_IMAGE_HOME_END_Y),
        imageOff       : apiConfig.imageOff || C.SOUND_BUTTON_IMAGE_OFF,
        imageOn        : apiConfig.imageOn || C.SOUND_BUTTON_IMAGE_ON,
        imageResultEndY: textSize / 2 + calcSize(2),
        imageStartY    : -100,
        imageWidth,
        imageX         : viewportSize.viewportWidth - imageWidth - calcSize(38),
        textAnchor     : apiConfig.textAnchor || TextAnchor.END,
        textFill       : apiConfig.textFill || C.SOUND_BUTTON_TEXT_FILL,
        textGameEndY   : scoreConfig.text2Y,
        textHomeEndY   : textSize / 2 + calcSize(18),
        textResultEndY : textSize / 2 + calcSize(15),
        textSize       : textSize + 'px',
        textStartY     : -100,
        textValueOff   : apiConfig.textValueOff || C.SOUND_BUTTON_TEXT_VALUE_OFF,
        textValueOn    : apiConfig.textValueOn || C.SOUND_BUTTON_TEXT_VALUE_ON,
        textWeight     : apiConfig.textWeight || TextWeight.BOLD,
        textX          : imageX - calcSize(10),
    };
};

export const convertStartButtonConfig = (
    apiConfig: APINinjaConfig.StartButton = {},
    homeLogoConfig: NinjaConfig.HomeLogo,
): NinjaConfig.StartButton => {
    const startX = calcSize(apiConfig.startX || C.START_BUTTON_ORIG_X);
    const endY   = homeLogoConfig.endY + homeLogoConfig.height + calcVerticalSize(327);
    const height = calcSize(apiConfig.height || C.START_BUTTON_HEIGHT);

    return {
        animLength: C.START_BUTTON_ANIMATION_LENGTH,
        endX      : startX,
        endY,
        height,
        image     : apiConfig.image || C.START_BUTTON_IMAGE,
        startX,
        startY    : viewportSize.viewportHeight,
        textAnchor: apiConfig.textAnchor || TextAnchor.MIDDLE,
        textEndX  : startX,
        textEndY  : endY - calcVerticalSize(8) + height / 2,
        textFill  : apiConfig.textFill || C.START_BUTTON_TEXT_FILL,
        textSize  : calcSize(apiConfig.textSize || C.START_BUTTON_TEXT_SIZE) + 'px',
        textStartX: viewportSize.viewportWidth / 2,
        textStartY: viewportSize.viewportHeight + height,
        textValue : apiConfig.textValue || C.START_BUTTON_TEXT_VALUE,
        textWeight: apiConfig.textWeight || TextWeight.BOLD,
        width     : calcSize(apiConfig.width || C.START_BUTTON_WIDTH),
    };
};
