import tween from '../libs/tween';
import timeline from '../scripts/timeline';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class Score {
    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    image: any;
    text1: any;
    text2: any;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    set () {
        const { LAYERS, SCORE_CONFIG } = this._config.options;

        this.image = this._layerManager.createImage(
            LAYERS.DEFAULT,
            SCORE_CONFIG.image,
            SCORE_CONFIG.imageSx,
            SCORE_CONFIG.imageY,
            SCORE_CONFIG.imageW,
            SCORE_CONFIG.imageH,
        ).hide();

        this.text1 = this._layerManager.createText(
            LAYERS.DEFAULT,
            SCORE_CONFIG.text1Value,
            SCORE_CONFIG.text1Sx,
            SCORE_CONFIG.text1Y,
            SCORE_CONFIG.text1Fill,
            SCORE_CONFIG.text1Size,
            SCORE_CONFIG.text1Weight,
        ).hide();

        this.text2 = this._layerManager.createText(
            LAYERS.DEFAULT,
            SCORE_CONFIG.text2Prefix + SCORE_CONFIG.text2Value,
            SCORE_CONFIG.text2Sx,
            SCORE_CONFIG.text2Y,
            SCORE_CONFIG.text2Fill,
            SCORE_CONFIG.text2Size,
            SCORE_CONFIG.text2Weight,
        ).hide();
    }

    show (start?: number) {
        const { MODES, SCORE_CONFIG } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : SCORE_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                SCORE_CONFIG.imageSx,
                SCORE_CONFIG.imageEx,
                SCORE_CONFIG.text1Sx,
                SCORE_CONFIG.text1Ex,
                SCORE_CONFIG.text2Sx,
                SCORE_CONFIG.text2Ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTime,
            onTimeEnd   : this.onTime,
            recycle     : this.anims,
        });
    }

    hide (start?: number) {
        const { MODES, SCORE_CONFIG } = this._config.options;

        timeline.createTask({
            start       : start,
            duration    : SCORE_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                SCORE_CONFIG.imageEx,
                SCORE_CONFIG.imageSx,
                SCORE_CONFIG.text1Ex,
                SCORE_CONFIG.text1Sx,
                SCORE_CONFIG.text2Ex,
                SCORE_CONFIG.text2Sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTime,
            onTimeEnd   : this.onTime,
            recycle     : this.anims,
        });
    }

    number (number: number) {
        this.text1.attr('text', number || 0);
        this.image.scale(1.2, 1.2);
        this.setTimeout(() => {
            this.image.scale(1, 1);
        }, 60);
    }

    onTimeUpdate (
        time: number,
        mode: Mode,
        isx: number,
        iex: number,
        t1sx: number,
        t1ex: number,
        t2sx: number,
        t2ex: number,
    ) {
        const { SCORE_CONFIG } = this._config.options;

        this.image.attr('x', this.anim(time, isx, iex - isx, SCORE_CONFIG.animLength));
        this.text1.attr('x', this.anim(time, t1sx, t1ex - t1sx, SCORE_CONFIG.animLength));
        this.text2.attr('x', this.anim(time, t2sx, t2ex - t2sx, SCORE_CONFIG.animLength));
    }

    onTime (mode: Mode) {
        [ this.image, this.text1, this.text2 ].invoke(mode);
    }
}
