import Ucren from '../libs/ucren';
import { makeElement } from '../scripts/utils';
import { viewportSize } from '../layout/viewport';
import { Config } from '../config';


export class LayerManager {
    num                                     = 2;
    layers: Partial<Record<LayerName, any>> = {};
    zIndexes: Record<LayerName, number>;

    constructor (private readonly _config: Config) {
        const { LAYERS } = this._config.options;

        this.zIndexes = {
            [LAYERS.PRELOAD]       : this.zi(),
            [LAYERS.DEFAULT]       : this.zi(),
            [LAYERS.FLASH]         : this.zi(),
            [LAYERS.JUICE]         : this.zi(),
            [LAYERS.KNIFE]         : this.zi(),
            [LAYERS.LIGHT]         : this.zi(),
            [LAYERS.FRUIT]         : this.zi(),
            [LAYERS.MASK]          : this.zi(),
            [LAYERS.START_SCREEN]  : this.zi(),
            [LAYERS.RESULT_SCREEN] : this.zi(),
            [LAYERS.BRAND_INTRO]   : this.zi(),
            [LAYERS.BRAND_QUESTION]: this.zi(),
        };
    }

    createText (
        layerName: LayerName,
        text: string,
        x: number,
        y: number,
        fill: string = '#fff',
        size: string = '14px',
        weight       = 'normal',
        textAnchor   = 'start',
    ) {
        const paper           = this.getLayer(layerName);
        const { FONT_FAMILY } = this._config.options;

        return paper.text(x, y, text).attr({
            'font-family': FONT_FAMILY,
            fill         : fill,
            'font-size'  : size,
            'font-weight': weight,
            'text-anchor': textAnchor,
        });
    }

    getLayer (layerName: LayerName) {
        const { viewportHeight, viewportWidth } = viewportSize;
        const { rootElement }                   = this._config;
        const { LAYERS }                        = this._config.options;
        layerName                               = layerName || LAYERS.DEFAULT;
        let paper                               = this.layers[layerName], layer;

        if (paper) return paper;

        layer = makeElement('div', {
            'class'    : 'layer', // @ts-ignore
            'data-name': layerName,
            'style'    : 'z-index: ' + (this.zIndexes[layerName] || 0) + ';',
        });
        Ucren.Element(rootElement).add(layer); // @ts-ignore
        paper = this.layers[layerName] = Raphael(layer, viewportWidth, viewportHeight);
        return paper;

    }

    createImage (layerName: LayerName, src: string, x: number, y: number, w: number, h: number, preserveAspectRatio = 'none') {
        const paper = this.getLayer(layerName);
        return paper.image(src, x, y, w, h, preserveAspectRatio);
    }

    hideLayer (layerName: LayerName) {
        const paper = this.getLayer(layerName);
        paper && paper.canvas && paper.canvas.parentNode && (paper.canvas.parentNode.style.display = 'none');
    }

    showLayer (layerName: LayerName) {
        const paper = this.getLayer(layerName);
        paper && paper.canvas && paper.canvas.parentNode && (paper.canvas.parentNode.style.display = 'block');
    }

    private zi () {
        return this.num = ++this.num || 2;
    }
}
