import { Config } from '../config';
import { LayerManager } from '../managers/layer.manager';
import timeline from '../scripts/timeline';
import { Knifes } from './knifes';


export class KnifePart {
    private readonly sx: number;
    private readonly sy: number;
    private readonly ex: number;
    private readonly ey: number;
    private line: any;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
        private readonly _knifes: Knifes,
        private conf: any,
    ) {
        this.sx = this.conf.sx;
        this.sy = this.conf.sy;
        this.ex = this.conf.ex;
        this.ey = this.conf.ey;
    }

    set () {
        const { KNIFE_PART_CONFIG, LAYERS } = this._config.options;
        const knifeLayer = this._layerManager.getLayer(LAYERS.KNIFE);
        let sx, sy, ex, ey, dx, dy, ax, ay;

        sx = this.sx;
        sy = this.sy;
        ex = this.ex;
        ey = this.ey;

        dx = sx - ex;
        dy = sy - ey;
        ax = Math.abs(dx);
        ay = Math.abs(dy);

        if (ax > ay) {
            sx += dx < 0 ? -1 : 1;
            sy += dy < 0 ? -(ay / ax) : ay / ax;
        } else {
            sx += dx < 0 ? -(ax / ay) : ax / ay;
            sy += dy < 0 ? -1 : 1;
        }

        this.line = knifeLayer.path(
            'M' + sx + ',' + sy + 'L' + ex + ',' + ey,
        ).attr({
            'stroke'      : KNIFE_PART_CONFIG.color,
            'stroke-width': KNIFE_PART_CONFIG.stroke + 'px',
        });

        timeline.createTask({
            start       : 0,
            duration    : KNIFE_PART_CONFIG.life,
            object      : this,
            onTimeUpdate: this.update,
            onTimeEnd   : this.end,
            recycle     : this._knifes.anims,
        });

        return this;
    }

    update (time: number) {
        const { KNIFE_PART_CONFIG } = this._config.options;

        this.line.attr('stroke-width', KNIFE_PART_CONFIG.stroke * (1 - time / KNIFE_PART_CONFIG.life) + 'px');
    }

    end () {
        this.line.remove();

        const index = this._knifes.indexOf(this);
        if (index) this._knifes.splice(index, 1);
    }
}
