import { DisplacementFactory } from '../factories/displacement.factory';
import { FruitFactory } from '../factories/fruit.factory';
import { SoundFactory } from '../factories/sound.factory';
import { LayerManager } from '../managers/layer.manager';
import { Background } from '../object/background';
import { Flash } from '../object/flash';
import { Fruit } from '../object/fruit';
import { Knifes } from '../object/knifes';
import { Lose } from '../object/lose';
import { Score } from '../object/score';
import { SoundButton } from '../object/sound-button';
import { PreloadFactory } from '../factories/preload.factory';
import { getAngleByRadian, pointToRadian } from './tools';
import message from './message';
import state from './state';
import timeline from './timeline';
import { Collide } from './collide';
import { Control } from './control';
import { Game } from './game';
import { ScenesManager } from '../managers/scenes.manager';
import { Config } from '../config';


export class Main {
    private setTimeout = timeline.setTimeout.bind(timeline);
    private readonly _game: Game;
    private readonly _knifes: Knifes;
    private readonly _scenesManager: ScenesManager;
    private readonly _control: Control;
    private readonly _collide: Collide;
    private readonly _fruitFactory: FruitFactory;
    private readonly _layerManager: LayerManager;
    private readonly _soundButton: SoundButton;
    private readonly _soundFactory: SoundFactory;
    private readonly _flash: Flash;
    private readonly _score: Score;
    private readonly _lose: Lose;
    private readonly _background: Background;
    private readonly _displacementFactory: DisplacementFactory;

    constructor (
        private readonly _config: Config,
    ) { // @ts-ignore
        window.Telegram?.WebApp?.disableVerticalScroll && window.Telegram.WebApp.disableVerticalScroll(true);

        const { MESSAGES, SCENES, STATE_KEYS, SCENE_STATES } = this._config.options;

        this._layerManager        = new LayerManager(this._config);
        this._displacementFactory = new DisplacementFactory(this._config, this._layerManager);
        this._knifes              = new Knifes(this._config, this._layerManager);
        this._control             = new Control(this._config, this._knifes);
        this._soundButton         = new SoundButton(this._config, this._layerManager);
        this._soundFactory        = new SoundFactory(this._soundButton);
        this._flash               = new Flash(this._config, this._layerManager, this._soundFactory);
        this._fruitFactory        = new FruitFactory(this._config, this._control, this._layerManager, this._flash);
        this._collide             = new Collide(this._fruitFactory);
        this._score               = new Score(this._config, this._layerManager);
        this._lose                = new Lose(this._config, this._layerManager);
        this._background          = new Background(this._config, this._layerManager);
        this._game                = new Game(this._config, this._fruitFactory, this._layerManager, this._knifes, this._soundFactory, this._score, this._lose, this._background);
        this._scenesManager       = new ScenesManager(this._config, this._game, this._layerManager, this._soundButton, this._soundFactory, this._flash, this._score, this._lose, this._background, this._displacementFactory);

        document.addEventListener('touchstart', (event) => {
            event.preventDefault();
        }, { passive: false });

        document.addEventListener('touchmove', (event) => {
            event.preventDefault();
        }, { passive: false });

        message.addEventListener(MESSAGES.SLICE, (knife: number[]) => {
            const fruits = this._collide.checkSlice(knife);

            if (fruits.length) {
                const angle = getAngleByRadian(pointToRadian(knife.slice(0, 2), knife.slice(2, 4)));
                fruits.forEach(fruit => message.postMessage(MESSAGES.SLICE_AT, fruit, angle));
            }
        });

        message.addEventListener(MESSAGES.TOUCH, (position: Point) => {
            const fruits = this._collide.checkTouch(position);

            if (fruits.length) {
                fruits.forEach(fruit => message.postMessage(MESSAGES.TOUCH_AT, fruit));
            }
        });

        message.addEventListener(MESSAGES.SLICE_AT, (fruit: Fruit, angle: number) => {
            if (state(STATE_KEYS.SCENE_STATE).isnot(SCENE_STATES.READY)) {
                return;
            }

            if (state(STATE_KEYS.SCENE_NAME).is(SCENES.GAME_BODY)) {
                this._game.sliceAt(fruit, angle);
            }
        });

        message.addEventListener(MESSAGES.TOUCH_AT, (fruit: Fruit) => {
            if (state(STATE_KEYS.SCENE_STATE).isnot(SCENE_STATES.READY)) {
                return;
            }

            if (state(STATE_KEYS.SCENE_NAME).is(SCENES.GAME_BODY)) {
                this._game.catchFruit(fruit);
            }
        });

        message.addEventListener(MESSAGES.START_GAME, () => {
            this._scenesManager.switchScene(SCENES.GAME_BODY);
        });

        new PreloadFactory(this._config, this._displacementFactory);
    }

    start () {
        const { SCENES } = this._config.options;

        [ timeline, this._scenesManager, this._control ].invoke('init');

        // @ts-ignore
        this.setTimeout(this._scenesManager.switchScene.saturate(this._scenesManager, SCENES.HOME_MENU), 3000);
    }
}
