import timeline from '../scripts/timeline';
import { randomNumber } from '../scripts/utils';
import { LayerManager } from '../managers/layer.manager';
import { Config } from '../config';


export class Background {
    image: any;
    time: Interval | null = null;

    constructor (
        private readonly _config: Config,
        private readonly _layerManager: LayerManager,
    ) {}

    set () {
        const { BACKGROUND_CONFIG, LAYERS } = this._config.options;

        this.image = this._layerManager.createImage(
            LAYERS.DEFAULT,
            BACKGROUND_CONFIG.image,
            BACKGROUND_CONFIG.x,
            BACKGROUND_CONFIG.y,
            BACKGROUND_CONFIG.width,
            BACKGROUND_CONFIG.height,
            BACKGROUND_CONFIG.preserveAspectRatio,
        );
    }

    getWobble = () => {
        const x = randomNumber(12) - 6;
        const y = randomNumber(12) - 6;
        this.image.attr({ x: x, y: y });
    };

    wobble () {
        this.time = timeline._setInterval(this.getWobble, 50);
    }

    stop = () => {
        this.time && this.time.stop();
        this.image.attr({ x: 0, y: 0 });
    };
}
